// pop-modal
.pop-modal {
	width: 100%;
	height: 100%;
	position: fixed;
	left: 0;
	top: 0;
	display: none;
	align-items: center;
	justify-content: center;
	z-index: 9999;

	&.is-active {
		display: flex;
	}

	.dim {
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.6);
		left: 0;
		top: 0;
		-webkit-animation: 0.2s ease 0s normal forwards 1 fadein;
		animation: 0.2s ease 0s normal forwards 1 fadein;
	}

	.pop-box {
		z-index: 1;
		display: flex;
		flex-direction: column;
		-webkit-animation: 0.4s ease 0s normal forwards 1 fadein;
		animation: 0.4s ease 0s normal forwards 1 fadein;
		max-width: 45rem;
		flex: 1 1 auto;
		background-color: $white;
		border-radius: 1.2rem;

		&.youtube {
			position: relative;
			background-color: transparent;
			max-width: 80rem;
			height: 60rem;

			.iframe-wrap {
				width: 100%;
				height: 100%;
				border-radius: 1.2rem;
				overflow: hidden;
			}

			.btn-close-pop {
				position: absolute;
				z-index: 10;
				top: -2.5rem;
				left: calc(100% - 2.5rem);
				border: none;
				border-radius: 0;
				width: 5rem;
				height: 5rem;
				padding: 0;
				border-radius: 50%;
				border: 1px solid rgba(255, 255, 255, 0.2);
				background: url(./../images/common/icon-close-48-white.svg) no-repeat rgba(255, 255, 255, 0.3) center;
				background-size: 3.6rem;
				transition: all 0.3s;

				&:hover {
					transform: rotate(180deg);
				}
			}
		}

		&.school-sch {
			max-width: 67rem;
			.apply-schlist {
				margin-top: 3rem;
				border-top: 1px solid $gray-700;
				li {
					display: flex;
					align-items: center;
					justify-content: space-between;
					flex: 1 1 auto;
					gap: 1rem;
					height: 9rem;
					border-bottom: 0.1rem solid $gray-200;
					span:nth-child(2) {
						width: 100%;
					}
					span:nth-child(3) {
						width: 30%;
					}
				}
			}
		}
	}

	.pop-top-area {
		position: relative;
		flex: none;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		height: 6.7rem;
		padding: 0 2.4rem;
		border-bottom: 1px solid $gray-100;

		h3 {
			font-size: 2rem;
			font-weight: 600;
		}

		.btn-close-pop {
			position: absolute;
			z-index: 10;
			right: 1.4rem;
			border: none;
			border-radius: 0;
			width: 5rem;
			height: 5rem;
			padding: 0;
			background: url(./../images/common/icon-close-black.svg) no-repeat center;
			background-size: 3.6rem;
			transition: all 0.3s;

			&:hover {
				transform: rotate(180deg);
			}
		}
	}

	.pop-con-area {
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 3.2rem;

		> * {
			width: 100%;
		}

		.text-area {
			text-align: left;
			font-size: 1.8rem;
			font-weight: 400;
			color: $gray-800;
		}

		.text-newline i {
			font-style: normal;
		}
	}

	.pop-footer {
		display: flex;
		justify-content: center;
		text-align: center;
		padding: 0 3.2rem 2.4rem;
		gap: 0.8rem;
	}
}

// pop-modal-alert
.pop-modal.popup-modal-alert {
	.pop-box {
		max-width: 35rem;
	}

	.pop-top-area {
		padding: 3.2rem;
	}

	.pop-con-area {
		min-height: 14rem;
		justify-content: center;

		.text-area {
			text-align: center;
		}
	}

	.pop-footer {
		padding: 0 2rem 2.4rem;
	}
}

@-webkit-keyframes fadein {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes fadein {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

/* mobile */
@media (max-width: 800px) {
	.pop-modal {
		.pop-box {
			max-width: 32rem;

			&.youtube {
				padding-top: 56%;
				max-width: calc(100% - 4rem);
				width: 100%;
				height: 0;

				.iframe-wrap > iframe {
					position: absolute;
					top: 0;
					left: 0;
				}

				.btn-close-pop {
					top: -2rem;
					left: calc(100% - 2rem);
					width: 4rem;
					height: 4rem;
				}
			}

			&.school-sch {
				max-width: calc(100% - 4rem);
				.apply-schlist {
					margin-top: 2rem;
				}
			}
		}

		.pop-top-area {
			height: 6.5rem;
			padding: 0 1.6rem;

			h3 {
				font-size: 1.8rem;
			}

			.btn-close-pop {
				width: 4rem;
				height: 4rem;
				right: 0.8rem;
				background-size: 3.2rem;
			}
		}

		.pop-con-area {
			padding: 2.4rem 1.6rem 2rem;

			.text-area {
				font-size: 1.6rem;
			}

			.text-newline i {
				display: block;
			}
		}

		.pop-footer {
			padding: 0 1.6rem 2rem;
			gap: 0.4rem;

			button {
				min-width: 14.2rem;
				height: 4.4rem;
			}
		}
	}

	.pop-modal.popup-modal-alert {
		.pop-box {
			max-width: 32rem;
		}

		.pop-top-area {
			padding: 3.2rem 1.6rem;
		}

		.pop-con-area {
			min-height: 8.4rem;
		}

		.pop-footer {
			padding: 0 2.4rem 2rem;
		}
	}
}
