/* header */
.header {
	position: absolute;
	left: 0;
	right: 0;
	z-index: 10;
	display: flex;
	align-items: stretch;
	justify-content: space-between;
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
	min-width: 1320px;
	height: 8rem;

	h1 {
		flex: 1 1 auto;
		height: 8rem;
		background-image: url('../images/main/pc-logo.svg');
		background-repeat: no-repeat;
		background-position: -2.5rem center;
		background-size: 33rem auto;
	}
	&-actions {
		display: flex;
		height: 8rem;
		.notify {
			&--button {
				width: 8rem;
				flex: none;
				border-radius: 0;
				border: none;
				position: relative;
				&:hover {
					.notify--badge {
						color: $blue-100;
					}
				}
				&.close {
					.notify--badge {
						display: none;
					}
					&::before {
						width: 4.8rem;
						height: 4.8rem;
						background-image: url('../images/common/icon-close-48-white.svg');
					}
				}
			}
			&--badge {
				width: 1.6rem;
				height: 1.6rem;
				display: flex;
				align-items: center;
				justify-content: center;
				position: absolute;
				bottom: 50%;
				left: calc(50% + 1px);
				transform: translate(-50%, -2px);
				border-radius: 100%;
				color: $blue-100;
				background-color: $white;
				font-size: 1.2rem;
				font-weight: 700;
			}
		}
		.menu--button {
			flex: 1 1 auto;
			border-radius: 0;
			border: none;
			padding-right: 12.2rem;
			background: $white url('../images/main/PC_MN_logo.png') no-repeat center right/12.2rem;
		}
	}
}

@media (max-width: 800px) {
	/* header */
	.header {
		min-width: auto;
		height: 6.4rem;
		h1 {
			flex: 1 1 auto;
			height: 6.4rem;
			background-position: center left 2rem;
			background-size: 15.8rem auto;
			background-image: url('../images/main/mo-logo.png');
		}
		&-actions {
			height: 6.4rem;
			.notify {
				&--button {
					width: 6.4rem;
					padding: 1.28rem;
					&.close {
						&::before {
							width: 3.8rem;
							height: 3.8rem;
						}
					}
				}
				&--badge {
					width: 1.2rem;
					height: 1.2rem;
					font-size: 8px;
					font-weight: 700;
				}
			}
			.menu--button {
				padding: 0;
				background: $white;
				width: 6.4rem;
				height: 6.4rem;
				> span {
					display: none;
				}
			}
		}
		&.fixed {
			position: fixed;
			left: 0;
			right: 0;
			top: 0;
			height: 6.4rem;
			background-color: $white;
			h1 {
				background-image: url('../images/main/mo-logo-bg-white.svg');
			}
		}
	}

	.scroll .header.fixed {
		background-color: transparent;
		h1 {
			background-image: url(../images/main/mo-logo.png);
		}
	}

	.bottomsheet .header.fixed {
		background-color: $white;
		h1 {
			background-image: url(../images/main/mo-logo-bg-white.svg);
		}
	}
}
