/* Footer PC */
.footer {
	padding: 4rem 2rem 5.3rem;
	background-color: $gray-800;
	min-width: 1320px;
	&__inner {
		width: 100%;
		max-width: 128rem;
		margin: 0 auto;
	}
	&__top {
		position: relative;
		padding-bottom: 3.2rem;
		border-bottom: 1px solid rgba(255, 255, 255, 0.06);
	}
	&__bottom {
		padding-top: 4.5rem;
		position: relative;
	}
	&__logo {
		display: block;
		width: 100%;
		height: 4rem;
		background: url(./../images/common/logo-footer.svg) no-repeat 0 50%;
	}
	&__menu {
		width: 100%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		gap: 4rem;

		margin-top: 5.2rem;

		li {
			color: $gray-400;
			font-size: 1.8rem;
			font-weight: 600;
			a {
				color: inherit;
				font-size: inherit;
				text-decoration: none;
			}
		}
	}
	&__address {
		li {
			display: flex;
			gap: 1rem;
			& + li {
				margin-top: 1.2rem;
			}
			> span {
				font-size: 1.6rem;
				color: $gray-600;
				font-weight: 600;
			}
			> p {
				font-size: 1.6rem;
				color: $gray-600;
				font-weight: 400;
				> span {
					font-size: 1.6rem;
					color: $gray-600;
					font-weight: 400;
				}
			}
		}
	}
	&__sns {
		position: absolute;
		right: 0;
		bottom: 3.2rem;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		gap: 1.9rem;
		& > li {
			width: 5.6rem;
			height: 5.6rem;
			font-size: 0;
			.go {
				display: block;
				width: 100%;
				height: 100%;
				border-radius: 50%;
				background: $white no-repeat center;
				font-size: 0;
				&.facebook {
					background-image: url(./../images/common/ico-facebook.svg);
				}
				&.youtube {
					background-image: url(./../images/common/ico-youtube.svg);
				}
				&.instagram {
					background-image: url(./../images/common/ico-instagram.svg);
				}
				&.blog {
					background-image: url(./../images/common/ico-blog.svg);
				}
			}
		}
	}
	&__copyright {
		position: absolute;
		right: 0;
		top: 3.2rem;
		.foot-bottom-text {
			color: $gray-700;
			font-size: 1.4rem;
		}
		.logo-adiga {
			display: block;
			flex: none;
			width: 100%;
			height: 5.6rem;
			border-radius: 0.8rem;
			margin-bottom: 0.8rem;
			font-size: 0;
			background: url(./../images/common/logo-adiga.svg) #222 no-repeat center;
		}
	}
}

@media (max-width: 800px) {
	/* Footer MO */
	.footer {
		padding: 4rem 2rem;
		min-width: auto;
		&__top {
			padding-bottom: 1.2rem;
		}
		&__bottom {
			padding-top: 2rem;
		}
		&__logo {
			height: 3rem;
			background-size: auto 3rem;
		}
		&__menu {
			flex-wrap: wrap;
			margin-top: 2rem;
			padding-bottom: 0;
			gap: 0;
			& > li {
				flex: 1 1 50%;
				height: 4rem;
				line-height: 4rem;
			}
		}
		&__sns {
			position: static;
			margin-top: 2rem;
			gap: 0.8rem;
			& > li .go {
				width: 4.8rem;
				height: 4.8rem;
			}
		}
		&__address {
			li {
				flex-direction: column;
				gap: 0.8rem;
				& + li {
					margin-top: 1.4rem;
				}
			}
		}
		&__copyright {
			margin-top: 2rem;
			position: static;
			.logo-adiga {
				width: 100%;
			}
		}
	}
}
