.sub {
	&.helper {
		.qna-table-wrap {
			margin-top: 6rem;
			> h4 {
				margin-bottom: 1.6rem;
			}

			& + .txt-tip {
				margin-top: 1.6rem;
				padding-top: 0;
			}
		}
		.type-ta {
			.searchText {
				display: flex;
				gap: 0 0.8rem;

				button[type='button'] {
					position: static;
					min-width: 12.4rem;
					height: 4.8rem;
					background: none;
					border: 1px solid $gray-300;
					padding: 0;
					background-color: $white;
					color: $gray-800;
					span {
						color: $gray-800;
					}
					&:hover {
						border-color: $gray-800;
					}
				}
			}
		}
	}
}
