/* main-head */
.main {
	&-visual {
		position: relative;
		min-width: 1320px;
		height: 100vh;
		max-width: 100%;
		background: url('../images/main/main-slide--01.jpg') no-repeat center center/100% 100%;
		/* swiper */
		.swiper {
			&.main-swiper {
				position: relative;
				max-width: 100%;
				height: 100%;
			}
			&-slide {
				> img {
					display: block;
					width: 100%;
					height: 100%;
					&.mo-only {
						display: none;
					}
				}
			}
			&-control {
				position: absolute;
				bottom: 6rem;
				left: 50%;
				transform: translateX(-50%);
				z-index: 10;
				.swiper-play-control {
					white-space: nowrap;
					.main-swiper {
						&-arrow {
							display: inline-block;
							position: relative;
							width: 2.5rem;
							height: 2.5rem;
							z-index: 10;
							background: no-repeat center;
							background-size: 2.5rem 2.5rem;
							top: 1px;
							vertical-align: middle;
						}
						&-button {
							&-prev {
								background-image: url('../images/common/icon-arrow-left-24-white.svg');
							}
							&-next {
								background-image: url('../images/common/icon-arrow-right-24-white.svg');
							}
						}
						&-pagination {
							display: inline-block;
							width: auto;
							text-align: center;
							transition: 0.3s opacity;
							transform: translate3d(0, 0, 0);
							z-index: 10;
							color: $white;
							vertical-align: middle;
							font-size: 1.4rem;
							line-height: 2.4rem;
							font-weight: 400;
							margin: auto 1rem;
							> span {
								color: $white;
								vertical-align: middle;
								font-size: 2.4rem;
								line-height: 2.4rem;
								font-weight: 600;
								margin-right: 1rem;
								&.swiper-pagination-total {
									font-weight: 400;
									font-size: 1.8rem;
									margin: 0 0 0 1rem;
									opacity: 0.6;
								}
							}
						}
					}
				}
				.btn-pause {
					display: inline-block;
					width: 2.5rem;
					height: 2.5rem;
					padding: 0;
					border: 0;
					font-size: 0;
					background: url('../images/common/icon-pause.svg') no-repeat 50%;
					background-size: 2.5rem auto;
					vertical-align: middle;
					&.pause {
						background-image: url('../images/common/icon-play-36-white.svg');
						background-size: 3rem auto;
					}
				}
			}
		}
	}
	&-slide-content {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		.slide {
			&-title {
				display: block;
				color: $white;
				font-size: 6.4rem;
				line-height: 7.6rem;
				text-align: center;
			}
			&-menu {
				&-buttons {
					display: flex;
					align-items: center;
					justify-content: center;
					flex-wrap: wrap;
					width: 55.6rem;
					margin-top: 8rem;
				}
				&--button {
					border: 1px solid rgba(255, 255, 255, 0.3);
					background-color: inherit;
					& > span {
						color: $white;
						font-size: 2rem;
						font-weight: 600;
					}
					&:hover {
						background-color: $blue-100;
						border-color: $blue-100;
					}
				}
			}
		}
	}
	.scroll-down {
		position: absolute;
		left: 5.72%;
		bottom: 0;
		width: 40px;
		height: 213px;
		background: rgba(0, 0, 0, 0.5);
		border-radius: 20px 20px 0 0;
		span {
			position: absolute;
			left: -26px;
			top: 60px;
			color: #fff;
			font-size: 14px;
			letter-spacing: 0;
			white-space: nowrap;
			transform: rotate(-90deg);
			transform-origin: center;
			text-transform: uppercase;
			font-weight: 100;
		}
		i {
			display: block;
			position: absolute;
			left: 50%;
			bottom: 0;
			width: 1px;
			height: 80px;
			background: rgba(255, 255, 255, 0.3);
			&::after {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				bottom: 0;
				z-index: 1;
				width: 1px;
				height: 30px;
				background: #fff;
				animation: scrollDown 1s ease-in alternate infinite;
			}
		}
	}
	@keyframes scrollDown {
		0% {
			transform: translateY(-50px);
		}
		100% {
			transform: translateY(0);
		}
	}
}

.sub {
	&-visual {
		flex: auto;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		gap: 8.6rem;
		align-items: center;
		min-width: 1320px;
		height: 38rem; // mo:26rem
		background-repeat: no-repeat;
		background-position: left top;
		background-size: cover;

		.page-title {
			h2 {
				color: $white;
				transition: all 0.3s;
			}
		}
	}
	nav {
		width: 100%;
		min-width: 1320px;
		margin: 0 auto;
		background-color: rgba(0, 0, 0, 0.3);
		backdrop-filter: blur(0.5rem);
		-webkit-backdrop-filter: blur(0.5rem);
		&.respo-pc {
			display: block;
		}
		&.respo-mo {
			display: none;
		}
		> ul {
			display: flex;
			min-width: 1320px;
			max-width: 1320px;
			margin: 0 auto;
			padding: 0 2rem;
			> li {
				height: 6.4rem;
				width: 100%;
				&:first-child {
					width: 6.4rem;
				}
				.btn-home {
					display: block;
					width: 6.4rem;
					height: 6.4rem;
					background: $primary-500 url(./../images/common/icon-home.svg) no-repeat center;
					background-size: 2.4rem;
				}
				a {
					display: flex;
					align-items: center;
					justify-content: center;
					height: 6.4rem;
					width: 100%;
					text-decoration: none;
					color: $white;
					transition: all 0.3s;
					&:hover {
						background-color: $white;
						color: $primary-500;
					}
				}
				&.on {
					a {
						background-color: $white;
						color: $primary-500;
						font-weight: 700;
					}
				}
			}
		}
	}
}

.helper .sub-visual {
	background-image: url(./../../assets/images/sub/PC-HP-bg.jpg);
}
.susi .sub-visual {
	background-image: url(./../../assets/images/sub/PC-SU-bg.jpg);
}
.jeongsi .sub-visual {
	background-image: url(./../../assets/images/sub/PC-JU-bg.jpg);
}
.pyeonip .sub-visual {
	background-image: url(./../../assets/images/sub/PC-PY-bg.jpg);
}
.foreigner .sub-visual {
	background-image: url(./../../assets/images/sub/PC-FO-bg.jpg);
}
.highschool .sub-visual {
	background-image: url(./../../assets/images/sub/PC-HS-bg.jpg);
}

@media (max-width: 800px) {
	.main {
		&-visual {
			min-width: auto;
			background-position: 80% right;
			background-size: cover;
			z-index: 2;
			/* swiper */
			.swiper {
				&-slide {
					& > img.mo-only {
						display: flex;
					}
				}
				&-control {
					bottom: 7.6rem;
					.swiper-play-control {
						.main-swiper {
							&-pagination {
								font-size: 1.6rem;
								line-height: 2rem;
								> span {
									font-size: 1.6rem;
									line-height: 2rem;
								}
							}
						}
					}
				}
			}
		}
		&-slide-content {
			.slide {
				&-title {
					font-size: 2.8rem;
					line-height: 1.25;
				}
				&-menu {
					&-buttons {
						width: 32rem;
						margin-top: 4rem;
					}
					&--button {
						min-width: auto;
						width: calc(50% - 4px);
						padding: 0 0.5rem;
						& > span {
							font-size: 1.2rem;
						}
					}
				}
			}
		}
		.scroll-down {
			display: none;
		}
	}
	.sub {
		&-visual {
			height: 26rem;
			min-width: 100%;
			gap: 4.2rem;
		}
		nav.respo-pc {
			display: none;
		}
		nav.respo-mo {
			display: block;
		}
		nav {
			min-width: 100%;
			max-width: 100%;
			margin: 0 auto;
			padding: 0;
			> ul {
				display: flex;
				padding: 0;
				min-width: 100%;
				max-width: 100%;
				margin: 0 auto;
				> li {
					height: 6rem;
					width: 100%;
					&:first-child {
						width: 100%;
					}
				}
			}
		}
	}

	.helper .sub-visual {
		background-image: url(./../../assets/images/sub/PC-HP-bg-mo.jpg);
	}
	.susi .sub-visual {
		background-image: url(./../../assets/images/sub/PC-SU-bg-mo.jpg);
	}
	.jeongsi .sub-visual {
		background-image: url(./../../assets/images/sub/PC-JU-bg-mo.jpg);
	}
	.pyeonip .sub-visual {
		background-image: url(./../../assets/images/sub/PC-PY-bg-mo.jpg);
	}
	.foreigner .sub-visual {
		background-image: url(./../../assets/images/sub/PC-FO-bg-mo.jpg);
	}
	.highschool .sub-visual {
		background-image: url(./../../assets/images/sub/PC-HS-bg-mo.jpg);
	}
}
