/* header */
.header {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 10;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  min-width: 1320px;
  height: 8rem;
}
.header h1 {
  flex: 1 1 auto;
  height: 8rem;
  background-image: url("../images/main/pc-logo.svg");
  background-repeat: no-repeat;
  background-position: -2.5rem center;
  background-size: 33rem auto;
}
.header-actions {
  display: flex;
  height: 8rem;
}
.header-actions .notify--button {
  width: 8rem;
  flex: none;
  border-radius: 0;
  border: none;
  position: relative;
}
.header-actions .notify--button:hover .notify--badge {
  color: #3860c6;
}
.header-actions .notify--button.close .notify--badge {
  display: none;
}
.header-actions .notify--button.close::before {
  width: 4.8rem;
  height: 4.8rem;
  background-image: url("../images/common/icon-close-48-white.svg");
}
.header-actions .notify--badge {
  width: 1.6rem;
  height: 1.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 50%;
  left: calc(50% + 1px);
  transform: translate(-50%, -2px);
  border-radius: 100%;
  color: #3860c6;
  background-color: #ffffff;
  font-size: 1.2rem;
  font-weight: 700;
}
.header-actions .menu--button {
  flex: 1 1 auto;
  border-radius: 0;
  border: none;
  padding-right: 12.2rem;
  background: #ffffff url("../images/main/PC_MN_logo.png") no-repeat center right/12.2rem;
}

@media (max-width: 800px) {
  /* header */
  .header {
    min-width: auto;
    height: 6.4rem;
  }
  .header h1 {
    flex: 1 1 auto;
    height: 6.4rem;
    background-position: center left 2rem;
    background-size: 15.8rem auto;
    background-image: url("../images/main/mo-logo.png");
  }
  .header-actions {
    height: 6.4rem;
  }
  .header-actions .notify--button {
    width: 6.4rem;
    padding: 1.28rem;
  }
  .header-actions .notify--button.close::before {
    width: 3.8rem;
    height: 3.8rem;
  }
  .header-actions .notify--badge {
    width: 1.2rem;
    height: 1.2rem;
    font-size: 8px;
    font-weight: 700;
  }
  .header-actions .menu--button {
    padding: 0;
    background: #ffffff;
    width: 6.4rem;
    height: 6.4rem;
  }
  .header-actions .menu--button > span {
    display: none;
  }
  .header.fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    height: 6.4rem;
    background-color: #ffffff;
  }
  .header.fixed h1 {
    background-image: url("../images/main/mo-logo-bg-white.svg");
  }
  .scroll .header.fixed {
    background-color: transparent;
  }
  .scroll .header.fixed h1 {
    background-image: url(../images/main/mo-logo.png);
  }
  .bottomsheet .header.fixed {
    background-color: #ffffff;
  }
  .bottomsheet .header.fixed h1 {
    background-image: url(../images/main/mo-logo-bg-white.svg);
  }
}
/* Footer PC */
.footer {
  padding: 4rem 2rem 5.3rem;
  background-color: #333333;
  min-width: 1320px;
}
.footer__inner {
  width: 100%;
  max-width: 128rem;
  margin: 0 auto;
}
.footer__top {
  position: relative;
  padding-bottom: 3.2rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}
.footer__bottom {
  padding-top: 4.5rem;
  position: relative;
}
.footer__logo {
  display: block;
  width: 100%;
  height: 4rem;
  background: url(./../images/common/logo-footer.svg) no-repeat 0 50%;
}
.footer__menu {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 4rem;
  margin-top: 5.2rem;
}
.footer__menu li {
  color: #ccc;
  font-size: 1.8rem;
  font-weight: 600;
}
.footer__menu li a {
  color: inherit;
  font-size: inherit;
  text-decoration: none;
}
.footer__address li {
  display: flex;
  gap: 1rem;
}
.footer__address li + li {
  margin-top: 1.2rem;
}
.footer__address li > span {
  font-size: 1.6rem;
  color: #888888;
  font-weight: 600;
}
.footer__address li > p {
  font-size: 1.6rem;
  color: #888888;
  font-weight: 400;
}
.footer__address li > p > span {
  font-size: 1.6rem;
  color: #888888;
  font-weight: 400;
}
.footer__sns {
  position: absolute;
  right: 0;
  bottom: 3.2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1.9rem;
}
.footer__sns > li {
  width: 5.6rem;
  height: 5.6rem;
  font-size: 0;
}
.footer__sns > li .go {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #ffffff no-repeat center;
  font-size: 0;
}
.footer__sns > li .go.facebook {
  background-image: url(./../images/common/ico-facebook.svg);
}
.footer__sns > li .go.youtube {
  background-image: url(./../images/common/ico-youtube.svg);
}
.footer__sns > li .go.instagram {
  background-image: url(./../images/common/ico-instagram.svg);
}
.footer__sns > li .go.blog {
  background-image: url(./../images/common/ico-blog.svg);
}
.footer__copyright {
  position: absolute;
  right: 0;
  top: 3.2rem;
}
.footer__copyright .foot-bottom-text {
  color: #666666;
  font-size: 1.4rem;
}
.footer__copyright .logo-adiga {
  display: block;
  flex: none;
  width: 100%;
  height: 5.6rem;
  border-radius: 0.8rem;
  margin-bottom: 0.8rem;
  font-size: 0;
  background: url(./../images/common/logo-adiga.svg) #222 no-repeat center;
}

@media (max-width: 800px) {
  /* Footer MO */
  .footer {
    padding: 4rem 2rem;
    min-width: auto;
  }
  .footer__top {
    padding-bottom: 1.2rem;
  }
  .footer__bottom {
    padding-top: 2rem;
  }
  .footer__logo {
    height: 3rem;
    background-size: auto 3rem;
  }
  .footer__menu {
    flex-wrap: wrap;
    margin-top: 2rem;
    padding-bottom: 0;
    gap: 0;
  }
  .footer__menu > li {
    flex: 1 1 50%;
    height: 4rem;
    line-height: 4rem;
  }
  .footer__sns {
    position: static;
    margin-top: 2rem;
    gap: 0.8rem;
  }
  .footer__sns > li .go {
    width: 4.8rem;
    height: 4.8rem;
  }
  .footer__address li {
    flex-direction: column;
    gap: 0.8rem;
  }
  .footer__address li + li {
    margin-top: 1.4rem;
  }
  .footer__copyright {
    margin-top: 2rem;
    position: static;
  }
  .footer__copyright .logo-adiga {
    width: 100%;
  }
}
/* main-head */
.main-visual {
  position: relative;
  min-width: 1320px;
  height: 100vh;
  max-width: 100%;
  background: url("../images/main/main-slide--01.jpg") no-repeat center center/100% 100%;
  /* swiper */
}
.main-visual .swiper.main-swiper {
  position: relative;
  max-width: 100%;
  height: 100%;
}
.main-visual .swiper-slide > img {
  display: block;
  width: 100%;
  height: 100%;
}
.main-visual .swiper-slide > img.mo-only {
  display: none;
}
.main-visual .swiper-control {
  position: absolute;
  bottom: 6rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}
.main-visual .swiper-control .swiper-play-control {
  white-space: nowrap;
}
.main-visual .swiper-control .swiper-play-control .main-swiper-arrow {
  display: inline-block;
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  z-index: 10;
  background: no-repeat center;
  background-size: 2.5rem 2.5rem;
  top: 1px;
  vertical-align: middle;
}
.main-visual .swiper-control .swiper-play-control .main-swiper-button-prev {
  background-image: url("../images/common/icon-arrow-left-24-white.svg");
}
.main-visual .swiper-control .swiper-play-control .main-swiper-button-next {
  background-image: url("../images/common/icon-arrow-right-24-white.svg");
}
.main-visual .swiper-control .swiper-play-control .main-swiper-pagination {
  display: inline-block;
  width: auto;
  text-align: center;
  transition: 0.3s opacity;
  transform: translate3d(0, 0, 0);
  z-index: 10;
  color: #ffffff;
  vertical-align: middle;
  font-size: 1.4rem;
  line-height: 2.4rem;
  font-weight: 400;
  margin: auto 1rem;
}
.main-visual .swiper-control .swiper-play-control .main-swiper-pagination > span {
  color: #ffffff;
  vertical-align: middle;
  font-size: 2.4rem;
  line-height: 2.4rem;
  font-weight: 600;
  margin-right: 1rem;
}
.main-visual .swiper-control .swiper-play-control .main-swiper-pagination > span.swiper-pagination-total {
  font-weight: 400;
  font-size: 1.8rem;
  margin: 0 0 0 1rem;
  opacity: 0.6;
}
.main-visual .swiper-control .btn-pause {
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  padding: 0;
  border: 0;
  font-size: 0;
  background: url("../images/common/icon-pause.svg") no-repeat 50%;
  background-size: 2.5rem auto;
  vertical-align: middle;
}
.main-visual .swiper-control .btn-pause.pause {
  background-image: url("../images/common/icon-play-36-white.svg");
  background-size: 3rem auto;
}
.main-slide-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main-slide-content .slide-title {
  display: block;
  color: #ffffff;
  font-size: 6.4rem;
  line-height: 7.6rem;
  text-align: center;
}
.main-slide-content .slide-menu-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 55.6rem;
  margin-top: 8rem;
}
.main-slide-content .slide-menu--button {
  border: 1px solid rgba(255, 255, 255, 0.3);
  background-color: inherit;
}
.main-slide-content .slide-menu--button > span {
  color: #ffffff;
  font-size: 2rem;
  font-weight: 600;
}
.main-slide-content .slide-menu--button:hover {
  background-color: #3860c6;
  border-color: #3860c6;
}
.main .scroll-down {
  position: absolute;
  left: 5.72%;
  bottom: 0;
  width: 40px;
  height: 213px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 20px 20px 0 0;
}
.main .scroll-down span {
  position: absolute;
  left: -26px;
  top: 60px;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0;
  white-space: nowrap;
  transform: rotate(-90deg);
  transform-origin: center;
  text-transform: uppercase;
  font-weight: 100;
}
.main .scroll-down i {
  display: block;
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 1px;
  height: 80px;
  background: rgba(255, 255, 255, 0.3);
}
.main .scroll-down i::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;
  width: 1px;
  height: 30px;
  background: #fff;
  animation: scrollDown 1s ease-in alternate infinite;
}
@keyframes scrollDown {
  0% {
    transform: translateY(-50px);
  }
  100% {
    transform: translateY(0);
  }
}

