  right: 0;
  top: 0;
  width: 90%;
  height: 100%;
  background: #ffffff;
}
.gnb-wrap.active, .gnb-wrap.slideBack {
  display: block;
  transition: all 0.7s;
}
.gnb-wrap h1 {
  z-index: 1;
  position: absolute;
  display: block;
  left: 3.7rem;
  top: 4rem;
  width: 23rem;
  height: 2.8rem;
  font-size: 0;
