  display: block;
  position: absolute;
  right: 8.3rem;
  top: 3rem;
  width: 4.8rem;
  height: 4.8rem;
  padding: 0;
  border: none;
  background: url(./../images/common/btn-close.svg) no-repeat center;
  transition: all 0.3s;
}
.gnb-wrap .btn-menu-close:hover {
  transform: rotate(180deg);
}
.gnb-wrap .depth1 {
  position: relative;
  background-color: #fafafa;
  height: 100%;
  width: 42rem;
  padding-top: 13rem;
}
.gnb-wrap .depth1 > li {
  position: relative;
  display: flex;
  justify-content: flex-end;
}
.gnb-wrap .depth1 > li > a,
.gnb-wrap .depth1 > li span {
  cursor: pointer;
  display: flex;
  height: 6.8rem;
  font-size: 3.2rem;
  font-weight: 700;
  padding-right: 6rem;
  color: #333333;
  opacity: 0.2;
  text-decoration: none;
  transition: all 0.2s;
}
.gnb-wrap .depth1 > li:hover > a,
.gnb-wrap .depth1 > li:hover span, .gnb-wrap .depth1 > li.on > a,
.gnb-wrap .depth1 > li.on span {
  color: #c93b3b;
  opacity: 1;
}
.gnb-wrap .depth1 > li:hover .depth2, .gnb-wrap .depth1 > li.on .depth2 {
  display: flex;
  transform: scaleX(1);
  opacity: 1;
  visibility: visible;
}
.gnb-wrap .depth1 > li:after {
  content: "";
  position: absolute;
  width: 0.4rem;
  height: 2.8rem;
  display: block;
  top: 0.8rem;
  right: 0;
  background-color: #c93b3b;
  transform: scaleY(0);
  transition: all 0.3s;
}
.gnb-wrap .depth1 > li:hover:after, .gnb-wrap .depth1 > li.on:after {
  transform: scaleY(1);
}
.gnb-wrap .depth1 > li .depth2 {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  top: 0.4rem;
  left: 42rem;
  width: calc(100vw - 46rem);
  transform-origin: left;
  opacity: 0;
  transform: scaleX(0);
  visibility: hidden;
  transition: all 0.3s;
}
.gnb-wrap .depth1 > li .depth2 > li {
  cursor: pointer;
  padding-top: 0.5rem;
  padding-left: 3rem;
  padding-bottom: 1rem;
}
.gnb-wrap .depth1 > li .depth2 > li > a {
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: #333333;
  font-size: 2rem;
  line-height: 2.6rem;
  font-weight: 700;
  opacity: 0.2;
  transition: all 0.3s;
}
.gnb-wrap .depth1 > li .depth2 > li > a:before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 0.2rem;
  background-color: #c93b3b;
  bottom: -0.1rem;
  transform: scaleX(0);
  transition: all 0.3s;
}
