    width: 2rem;
    height: 2rem;
    background: url(./../images/common/btn-close.svg) no-repeat center;
  }
  .gnb-wrap .depth1 {
    width: 16rem;
    padding-top: 9rem;
    border-right: 1px solid #eee;
  }
  .gnb-wrap .depth1 > li {
    position: relative;
    display: flex;
    justify-content: flex-start;
  }
  .gnb-wrap .depth1 > li > a,
  .gnb-wrap .depth1 > li span {
    height: 4.4rem;
    font-size: 2.4rem;
    font-weight: 700;
    padding-left: 3rem;
    padding-right: inherit;
    color: #333333;
    opacity: 0.2;
    text-decoration: none;
    transition: all 0.2s;
  }
  .gnb-wrap .depth1 > li:hover > a,
  .gnb-wrap .depth1 > li:hover span, .gnb-wrap .depth1 > li.on > a,
  .gnb-wrap .depth1 > li.on span {
    color: #c93b3b;
    opacity: 1;
  }
  .gnb-wrap .depth1 > li:hover .depth2, .gnb-wrap .depth1 > li.on .depth2 {
    display: block;
    transform: scaleX(1);
    opacity: 1;
    visibility: visible;
  }
  .gnb-wrap .depth1 > li:after {
    content: "";
    position: absolute;
    width: 0.4rem;
    height: 2rem;
    display: block;
    top: 0.8rem;
    right: 0;
    background-color: #c93b3b;
    transform: scaleY(0);
    transition: all 0.3s;
  }
  .gnb-wrap .depth1 > li:hover:after {
    transform: scaleY(1);
  }
  .gnb-wrap .depth1 > li .depth2 {
    position: absolute;
    display: block;
    top: 0.3rem;
    left: 16rem;
    width: calc(100vw - 16rem);
    opacity: 0;
    transform-origin: left;
    transform: scaleX(0);
    visibility: hidden;
    transition: all 0.3s;
  }
  .gnb-wrap .depth1 > li .depth2 > li {
    cursor: pointer;
    padding-left: 3rem;
    padding-bottom: 1rem;
  }
  .gnb-wrap .depth1 > li .depth2 > li > a {
    position: relative;
    display: inline-block;
    text-decoration: none;
    color: #666666;
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 2.2rem;
    opacity: 1;
    transition: all 0.3s;
  }
  .gnb-wrap .depth1 > li .depth2 > li > a:before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 0.2rem;
    background-color: #c93b3b;
    bottom: -0.1rem;
    transform: scaleX(0);
    transition: all 0.3s;
  }
  .gnb-wrap .depth1 > li .depth2 > li:hover > a {
    color: #c93b3b;
    opacity: 1;
  }
  .gnb-wrap .depth1 > li .depth2 > li:hover > a:before {
    transform: scaleX(1);
  }
