    padding: 1.2rem 0 0 0;
  }
  .brochure-card-hover > strong {
    color: #333333;
    font-size: 2rem;
    line-height: 2.4rem;
    text-align: center;
  }
  .brochure-card-hover button.btn-icon.download--button {
    display: none;
  }
  /* _AJOU Information */
  .ajou-info-button--section {
    margin-top: 2.4rem;
    padding: 0 2rem;
    gap: 0.8rem;
  }
  .ajou-info-button--section .middle--link {
    width: 100%;
    height: 15rem;
    flex: none;
    font-size: 2.4rem;
  }
  .ajou-info-button--section .quick-menu--area {
    flex: 1;
    padding: 2.4rem;
  }
  .ajou-info-button--section .quick-menu--area.row {
    padding: 3rem 3.2rem;
  }
  .ajou-info-button--section .quick-menu--area.row .quick-menu--link {
    max-width: none;
    width: 100%;
    flex: none;
  }
  .ajou-info-button--section .quick-menu-title {
    padding: 1.2rem 0;
  }
  .ajou-info-button--section .quick-menu-title strong {
    font-size: 2.4rem;
    line-height: 3.2rem;
  }
  .ajou-info-button--section .quick-menu-list {
    margin-top: 2.4rem;
  }
  .ajou-info-button--section .quick-menu--link {
    display: inline-flex;
    flex: 100%;
    max-width: 100%;
    text-decoration: none;
    padding: 1.6rem 2.4rem 1.6rem 0;
  }
  .ajou-info-button--section .quick-menu--link span {
    font-size: 1.8rem;
    line-height: 1.8rem;
  }
  .ajou-info-button--ver {
    width: 100%;
    flex: none;
    gap: 0.8rem;
  }
  .ajou-info-button--ver > a {
    flex-direction: row;
    align-items: center;
    padding: 2.8rem;
  }
  .ajou-info-button--ver > a strong {
    margin: 0;
    font-size: 2rem;
    line-height: 2.4rem;
    padding-right: 3rem;
    background: url(../images/common/icon-arrowbar-right.svg) no-repeat bottom right/2.4rem;
  }
  .ajou-info-button--ver > a:hover strong {
    background: transparent;
  }
  /* subContents _0.enterInfo */
  .sub-content {
    padding: 8rem 2rem 8rem;
  }
  .sub-content-inner::after {
    width: 17.8rem;
    height: 7rem;
  }
  .sub-content.enterInfo {
    background-position: left -4rem bottom 6rem;
  }
  .sub-content.enterInfo::before {
    width: 6.3rem;
    height: 6rem;
    left: 0;
  }
  .sub-content .content-title {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  .sub-content .content-title > strong {
    font-size: 2.8rem;
    line-height: 3.6rem;
    width: 100%;
  }
  .sub-content .content-title > strong:last-child {
    width: auto;
  }
  .sub-content .content-title .swiper-wrapper {
    margin-top: 2.4rem;
  }
  /* ajou youtube */
  .ajou-youtube-swiper .swiper-wrapper {
    margin-top: 2.4rem;
  }
  .ajou-youtube-swiper-wrap::before {
    width: 8rem;
    height: 8rem;
    left: 50%;
    transform: translateX(-50%);
  }
  .ajou-youtube-swiper-controller {
    top: 3.6rem;
  }
  .ajou-youtube-swiper-arrow {
    width: 3.6rem;
    height: 3.6rem;
  }
  .youtube-card-title {
    margin-top: 1.2rem;
    font-size: 2rem;
    font-weight: 600;
    text-align: center;
  }
  /* // 0. */
  /* subContents 5. _ */
  .program-apply-area {
    height: auto;
    flex-direction: column;
    width: calc(100% - 4rem);
    margin: 0 auto;
  }
  .program-apply--link {
    flex-direction: row;
    gap: 0.8rem;
  }
  .program-apply--link strong {
    font-size: 2rem;
    line-height: 2.4rem;
  }
  /* gaia */
  .gaia-info {
    flex: none;
    width: 100%;
  }
  .gaia-info-area {
    flex-direction: column;
    gap: 0;
    max-height: none;
  }
  .gaia-info-area .image-wrapper {
    position: absolute;
    top: 7rem;
    max-width: 40rem;
  }
  .gaia-info-area .image-wrapper > img {
    display: inline-block;
    width: 100%;
    height: 100%;
  }
  .gaia-info-area .content-title > strong {
    font-size: 2.8rem;
    line-height: 3.6rem;
    width: auto;
    flex: none;
    gap: 0.4rem;
  }
  .gaia-info-area::before {
    width: 8rem;
    height: 8rem;
    top: 0;
    left: 20rem;
  }
  .gaia-info--desc {
    margin-top: 24.8rem;
  }
  .gaia-info--desc p {
    margin-top: 2.4rem;
    font-size: 1.8rem;
    line-height: 2.4rem;
  }
  .gaia-info--desc p > strong {
    font-weight: 600;
  }
  .gaia-info--desc p + p {
    margin-top: 1.2rem;
  }
  .gaia-info-sns {
    margin-top: 4rem;
    display: flex;
    align-items: center;
    gap: 0.4rem;
  }
  .gaia-info-sns .sns--link {
    padding: 1.4rem 0;
    flex: 1;
  }
  .gaia-info-sns .sns--link.sns--link {
    margin: 0;
    background-image: none;
  }
  .gaia-info-sns .sns--link span {
    text-align: center;
    font-size: 1.6rem;
    line-height: 2rem;
    width: 100%;
    display: inline-block;
  }
  /* // 5.  */
  .ico {
    width: 3.2rem;
    height: 3.2rem;
  }
  .ico.youtube-logo {
    width: 3.6rem;
    height: 3.6rem;
    margin-left: 0;
    background-size: 100% 2.4rem;
  }
  /* bottom sheet */
  .mo-tab {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
  .mo-tab.fixed {
    position: fixed;
    top: 6.4rem;
    bottom: auto;
  }
  .mo-tab.fixed > button {
    background-color: #0754b4;
  }
  .mo-tab-nav--button {
    width: 100%;
    border: 0;
    border-radius: 0;
    padding: 2rem 1.6rem 2rem 1.6rem;
    justify-content: flex-start;
    background: rgba(0, 0, 0, 0.24) url("../images/common/icon-arrow-down-20-white.svg") no-repeat center right 1.6rem/2rem;
  }
  .mo-tab-nav--button > span {
    color: #ffffff;
    font-size: 1.6rem;
    line-height: 2rem;
    font-weight: 500;
  }
  .tab-nav--bottomsheet {
    display: none;
    z-index: 2;
  }
  .tab-nav--bottomsheet.active {
    display: block;
    width: 100%;
    height: 100%;
  }
  .tab-nav--bottomsheet.active .bottomsheet {
    z-index: 5;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #ffffff;
    border-top: 1px solid #eee;
    border-radius: 1.2rem 1.2rem 0 0;
  }
  .tab-nav--bottomsheet.active .bottomsheet-header {
    padding: 2rem 1.6rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .tab-nav--bottomsheet.active .bottomsheet-header strong {
    flex: none;
    font-size: 1.8rem;
    line-height: 2.4rem;
    color: #111111;
  }
  .tab-nav--bottomsheet.active .bottomsheet-header .close--button {
    border: none;
    padding: 0;
    width: 3.2rem;
    height: 3.2rem;
  }
  .tab-nav--bottomsheet.active .bottomsheet-menu--link {
    display: inline-block;
    width: 100%;
    height: 100%;
    padding: 2rem 1.2rem;
    text-decoration: none;
    border-top: 1px solid #eee;
  }
  .tab-nav--bottomsheet.active .bottomsheet-menu--link > strong {
    font-size: 1/6rem;
    line-height: 2rem;
  }
  .tab-nav--bottomsheet.active > .dim {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 3;
  }
}
