.floating-menu {
	position: fixed;
	right: 4rem;
	bottom: 4rem;
	z-index: 10;
	& > button {
		width: 8rem;
		height: 8rem;
		margin-bottom: 1.2rem;
		padding: 0;
		border: 0;
		font-size: 0;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.btn-chatbot {
		height: 10.3rem;
		background: url('../images/common/icon-chatbot.png') no-repeat 50% 0;
	}
	.btn-top {
		display: none;
		border: 1px solid $primary-100;
		border-radius: 50%;
		background: url('../images/common/icon-top.svg') #fff no-repeat 50%;
	}
}

@media (max-width: 800px) {
	.floating-menu {
		right: 1.2rem;
		& > button {
			width: 5.2rem;
			height: 5.2rem;
			margin-bottom: 0.4rem;
		}
		.btn-top {
			background-size: 2.7rem auto;
		}
		.btn-chatbot {
			height: 6.8rem;
			background-size: cover;
			background-image: url('../images/common/icon-chatbot-mo.png');
		}
	}
}
