/* gnb */

.gnb {
	&-wrapper {
		display: none;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 2;

		&.on {
			display: block;
			background-color: $gray-300;
		}
	}
	padding-top: 8rem;
	background: url('../images/main/img-gnb-bg.jpg') no-repeat center center/100% auto;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;

	&-list {
		margin: auto auto auto 20rem;
		overflow-y: auto;
	}

	&--title {
		display: block;
		font-size: 4rem;
		line-height: 4.8rem;
		font-weight: 700;
		color: white;
		flex: none;
		cursor: pointer;
		margin-right: 4rem;
		&::before {
			content: '';
			display: inline-block;
			width: 0;
			height: 4px;
			margin-right: 2.4rem;
			transition: width 0.3s ease-in-out;
			vertical-align: middle;
		}
	}
	&-item {
		display: flex;
		align-items: center;
		position: relative;
		&:hover {
			.gnb-sub--list {
				display: flex;
			}
			.gnb--title::before {
				width: 10rem;
			}
		}
		& + .gnb-item {
			margin-top: 4rem;
		}
		&.entryInfo {
			.gnb--title::before {
				background-color: $primary-200;
			}
		}
		&.early {
			.gnb--title::before {
				background-color: $greenblue-100;
			}
		}
		&.regular {
			.gnb--title::before {
				background-color: #31bc69;
			}
		}
		&.transfer {
			.gnb--title::before {
				background-color: $yellow-100;
			}
		}
		&.overseas {
			.gnb--title::before {
				background-color: $blue-100;
			}
		}
		&.highschool {
			.gnb--title::before {
				background-color: $purple-100;
			}
		}
	}

	&-sub {
		&--list {
			display: none;
			gap: 5.6rem;
			flex: 2 2 auto;
		}
	}

	.sub {
		&--link {
			display: block;
			color: $white;
			text-decoration: none;
			span {
				color: $white;
				font-size: 2.4rem;
				line-height: 3.2rem;
				font-weight: 600;
			}
			&:hover {
				text-decoration: underline;
			}
		}
	}
}

/* mobile */
@media (max-width: 800px) {
	.gnb {
		padding-top: 6.4rem;
		background-size: auto 100%;
		&-list {
			margin: 4rem 2rem;
		}

		&--title {
			width: 100%;
			font-size: 2.4rem;
			line-height: 3.2rem;
			margin: 0;
			padding: 1.2rem 2.8rem 1.2rem 0;
			background-image: url('../images/common/icon-arrow-down-28-white.svg');
			background-repeat: no-repeat;
			background-position: center right;
			background-size: 2.8rem auto;
			&::before {
				display: none;
			}
		}
		&-item {
			flex-direction: column;
			&:hover {
				.gnb-sub--list {
					display: none;
				}
			}
			&.active {
				.gnb-sub--list {
					display: flex;
					flex-wrap: wrap;
					width: 100%;
				}
				.gnb--title {
					border-bottom: 2px solid transparent;
					background-image: url('../images/common/icon-arrow-up-28-white.svg');
				}

				&.entryInfo {
					.gnb--title {
						border-bottom-color: $primary-200;
					}
				}
				&.early {
					.gnb--title {
						border-bottom-color: $greenblue-100;
					}
				}
				&.regular {
					.gnb--title {
						border-bottom-color: #31bc69;
					}
				}
				&.transfer {
					.gnb--title {
						border-bottom-color: $yellow-100;
					}
				}
				&.overseas {
					.gnb--title {
						border-bottom-color: $blue-100;
					}
				}
				&.highschool {
					.gnb--title {
						border-bottom-color: $purple-100;
					}
				}
			}
		}

		&-sub {
			&--list {
				gap: 0;
				margin: 1.2rem 0 1.6rem;
			}
		}

		.sub {
			&-item {
				width: 50%;
			}
			&--link {
				padding: 1.2rem 0;
				span {
					font-size: 1.8rem;
					line-height: 2.4rem;
				}
			}
		}
	}
}
