.highschool {
	.qna-table-wrap {
		margin-top: 6rem;
		> h4 {
			margin-bottom: 1.6rem;
		}
		.searchText {
			display: flex;
			gap: 0 0.8rem;
			button[type='button'] {
				position: static;
				min-width: 8rem;
				height: 4.8rem;
				background: none;
				border: 1px solid $primary-400;
				background-color: $primary-400;
				span {
					color: $white;
				}
				&:hover {
					border-color: $primary-500;
					background-color: $primary-500;
				}
			}
		}
	}
	.inputText > span {
		margin-left: 0.8rem;
		color: $gray-700;
	}
	.checkBox > label {
		height: auto;
	}
}

@media (max-width: 800px) {
	.highschool {
		.qna-table-wrap {
			.checkBox-wrap {
				flex-direction: row;
				flex-wrap: wrap;
				align-items: center;
			}
		}
	}
}
