h1 {
	background: url(./../images/common/logo-white.svg) no-repeat center;
	font-size: 0;
	width: 20.9rem;
	height: 3.2rem;
}
.color {
	h1 {
		background: url(./../images/common/logo-color.svg) no-repeat center;
		background-size: 20.9rem;
	}
}
h2 {
	font-size: 6.4rem;
}
h3 {
	font-size: 4rem;
}
h4 {
	font-size: 2.4rem;
}
h1,
h2,
h3,
h4 {
	color: $gray-900;
}
h5,
p,
span,
input,
legend,
label,
li,
dt,
dd,
a,
th,
td {
	font-size: $normalSize;
	line-height: 1.4;
	color: $gray-800;
}

body {
	// add body fixed class - modal
	&.is-fixed {
		overflow: hidden;
		width: 100%;
		height: 100%;
	}
}

/*  */
button {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 0.6rem;
	padding: 0 2rem;
	transition: all 0.3s;

	/* Color */
	&.base {
		border: 1px solid $gray-300;
		background-color: $white;
		&:hover {
			border-color: $gray-800;
		}
	}
	&.blue {
		border: 1px solid $primary-400;
		background-color: $primary-400;
		color: $white;
		span {
			color: $white;
		}
		&:hover {
			border-color: $primary-500;
			background-color: $primary-500;
			color: $white;
			span {
				color: $white;
			}
		}
	}
	&.brown {
		border: 1px solid $gray-600;
		background-color: $gray-600;
		color: $white;
		span {
			color: $white;
		}
		&:hover {
			border-color: #584d48;
			background-color: #584d48;
			color: $white;
			span {
				color: $white;
			}
		}
	}

	&.primary {
		border: 1px solid $primary-400;
		background-color: $primary-400;
		color: $white;
		span {
			color: $white;
		}
		&:hover {
			border-color: $primary-500;
			background-color: $primary-500;
		}
	}

	&.secondary {
		border: 1px solid $primary-10;
		background-color: $primary-10;
		color: $primary-400;
		span {
			color: $primary-400;
		}
		&:hover {
			border-color: $primary-50;
			background-color: $primary-50;
		}
	}

	&.outline {
		border: 1px solid $gray-300;
		background-color: $white;
		color: $gray-800;
		span {
			color: $gray-800;
		}
		&:hover {
			border-color: $gray-800;
		}
	}

	/* Disabled */
	&:disabled {
		border: 1px solid $gray-100;
		background-color: $gray-50;
		color: $gray-500;
		span {
			color: $gray-500;
		}
		&:hover {
			border-color: $gray-100;
			background-color: $gray-50;
			color: $gray-500;
			span {
				color: $gray-500;
			}
		}
	}
	/* Size */
	&.btn-l {
		height: 7.2rem;
		min-width: 18rem;
		> span {
			font-size: 2rem;
			font-weight: 600;
		}
	}
	&.btn-m {
		height: 5.6rem;
		min-width: 16rem;
		> span {
			font-size: 1.8rem;
			font-weight: 500;
		}
	}
	&.btn-s {
		height: 4.8rem;
		min-width: 15rem;
		font-size: 1.6rem;
		border-radius: 0.6rem;
		padding: 0 1.2rem;
	}
}

/*  */
.text-ellip {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}
.text-ellip-v2 {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
}

code {
	background-color: $white;
	color: $gray-800;
	font-size: 1.2rem;
	font-weight: 500;
	display: block;
	padding: 0;
	line-height: 1.6;
	border-radius: 0.6rem;
	&.bg-black {
		background-color: $gray-800;
		color: $gray-50;
		padding: 0.8rem 1rem;
		font-weight: 400;
	}
}

/* Title Area */
.title-area {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.setting-area {
	display: flex;
	justify-content: flex-end;
	> ul {
		display: flex;
		gap: 3rem;
		align-items: center;
		> li:first-child {
			position: relative;
			&:after {
				content: '';
				position: absolute;
				display: block;
				right: -1.5rem;
				top: calc(50% - 0.8rem);
				background-color: $gray-300;
				width: 1px;
				height: 1.6rem;
			}
		}
		> li {
			display: flex;
			align-items: center;
			.icon-reduction,
			.icon-magnification,
			.icon-font,
			.icon-print,
			.icon-refresh {
				padding: 0;
				width: 3rem;
				height: 3rem;
				border: none;
				font-size: 0;
			}
			.icon-reduction {
				background: transparent url(./../images/common/icon-reduction.svg) no-repeat center;
			}
			.icon-magnification {
				background: transparent url(./../images/common/icon-magnification.svg) no-repeat center;
			}
			.icon-font {
				background: transparent url(./../images/common/icon-font.svg) no-repeat center;
			}
			.icon-print {
				background: transparent url(./../images/common/icon-print.svg) no-repeat center;
			}
			.icon-refresh {
				background: transparent url(./../images/common/icon-refresh.svg) no-repeat center;
			}
		}
	}
}

/* Tab */
.title-area + .tab,
.title-area + .mo-content-wrap {
	margin-top: 4rem;
}
.tab {
	display: flex;
	border-bottom: 1px solid $gray-100;
	> li {
		width: 100%;
		> button {
			position: relative;
			border: none;
			padding: 0;
			width: 100%;
			height: 8rem;
			font-size: 2.4rem;
			color: $gray-400;
			&:after {
				content: '';
				position: absolute;
				display: block;
				width: 100%;
				height: 0.2rem;
				bottom: 0;
				left: 0;
				background-color: $primary-500;
				transition: all 0.3s;
				transform: scaleX(0);
			}
			&:hover {
				color: $primary-500;
				font-weight: 600;
			}
			&:hover:after {
				transform: scaleX(1);
			}
			&.tabOn {
				color: $primary-500;
				font-weight: 600;
				&:after {
					transform: scaleX(1);
				}
			}
		}
	}
}

/* Input  */
input {
	color: $gray-900;
	border: 1px solid $gray-300;
	border-radius: 0.6rem;
	&:hover {
		border: 1px solid $gray-800;
	}
	&::-webkit-input-placeholder {
		color: $gray-500;
	}
	&::-moz-placeholder {
		color: $gray-500;
	}
	&:-ms-input-placeholder {
		color: $gray-500;
	}
	&::-ms-input-placeholder {
		color: $gray-500;
	}
	&::placeholder {
		color: $gray-600;
	}
	&:disabled {
		color: $gray-400;
		background-color: $gray-50;
		&:hover {
			border: 1px solid $gray-300;
		}
		&::-webkit-input-placeholder {
			color: $gray-400;
		}
		&::-moz-placeholder {
			color: $gray-400;
		}
		&:-ms-input-placeholder {
			color: $gray-400;
		}
		&::-ms-input-placeholder {
			color: $gray-400;
		}
		&::placeholder {
			color: $gray-400;
		}
	}
}
input[type='text'],
input[type='password'],
input[type='tel'] {
	position: relative;
	display: flex;
	align-items: center;
	height: 4.8rem;
	width: 100%;
	padding: 0 1.5rem;
}
.inputDate,
.inputText {
	display: flex;
	align-items: center;
	label {
		margin-right: 1rem;
		flex: none;
	}
	input {
		position: relative;
		display: flex;
		align-items: center;
		height: 4rem;
		width: 100%;
		padding: 0 1.5rem;
		&.search {
			background-image: url(./../images/common/icon-searchbox.svg);
			background-repeat: no-repeat;
			background-position: center right 1.2rem;
		}
	}
}
.inputDate {
	input {
		width: 32rem;
		background-image: url(./../images/common/icon-calendar.svg);
		background-repeat: no-repeat;
		background-position: center right 1.2rem;
		&:disabled {
			background-image: url(./../images/common/icon-calendar-disabled.svg);
		}
	}
	.datepicker-dropdown {
		.datepicker-picker {
			box-shadow: none;
			border: 1px solid $gray-300;
			border-radius: 0.6rem;
			.datepicker-header {
				padding: 2rem 2rem 0;
				.datepicker-controls {
					.button {
						width: 2.4rem;
						height: 2.4rem;
						padding: 0;
						&.prev-button {
							flex: none;
							font-size: 0;
							background: url(./../images/common/icon-arrow-left-24.svg) no-repeat center;
						}
						&.next-button {
							flex: none;
							font-size: 0;
							background: url(./../images/common/icon-arrow-right-24.svg) no-repeat center;
						}
						&.view-switch {
							font-size: 1.8rem;
						}
					}
				}
			}
			.datepicker-main {
				padding: 2rem;
				.datepicker-view {
					width: 27.8rem;
					.datepicker-cell {
						height: 4rem;
						border-radius: 50%;
						&.focused {
							background-color: $primary-500;
							color: $white;
						}
					}
				}
			}
		}
	}
}
.searchText {
	position: relative;
	display: flex;
	align-items: center;
	label {
		margin-right: 1rem;
		flex: none;
	}
	input {
		position: relative;
		display: block;
		height: 4rem;
		width: 100%;
		padding: 0 4.2rem 0 1rem;
		/*  */
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		&:disabled + button {
			background: url(./../images/common/icon-searchbox.svg) no-repeat center;
		}
	}
	button {
		position: absolute;
		display: block;
		right: 0;
		width: 4.2rem;
		height: 4rem;
		border: none;
		background: url(./../images/common/icon-searchbox.svg) no-repeat center;
		&.red {
			background: url('./../images/common/icon_search_red.svg') no-repeat center;
		}
	}
}

/* Radio, Check */
.radioButton {
	> input[type='radio'] {
		position: absolute;
		left: -9999px;
		&:checked + label {
			&:before {
				opacity: 0;
			}
			&:after {
				opacity: 1;
				transform: scale(1);
			}
		}
		&:disabled + label {
			&:before {
				background: url(./../images/common/icon-radio-disable.svg) no-repeat center;
				opacity: 1;
			}
			&:after {
				opacity: 0;
				transform: scale(0);
			}
		}
		&:checked:disabled + label {
			&:before {
				opacity: 0;
			}
			&:after {
				opacity: 0.4;
				transform: scale(1);
			}
		}
	}
	> label {
		position: relative;
		display: flex;
		align-items: center;
		gap: 0.3rem;
		height: 2.4rem;
		&:before,
		&:after {
			content: '';
			display: block;
			width: 2.4rem;
			height: 2.4rem;
			left: 0;
			top: 0;
			transition: all 0.3s;
		}
		&:before {
			position: relative;
			background: url(./../images/common/icon-radio.svg) no-repeat center;
			background-size: 2.4rem;
			opacity: 1;
		}
		&:after {
			position: absolute;
			background: url(./../images/common/icon-radio-active.svg) no-repeat center;
			background-size: 2.4rem;
			opacity: 0;
			transform: scale(0);
		}
		> span {
			margin-bottom: 0.1rem;
		}
	}
}
.expl {
	font-size: 1.6rem;
	color: $gray-500;
	font-weight: 400;
}
.checkBox-wrap {
	display: flex;
	align-items: center;
	gap: 1rem;
	.checkBox-bord {
		display: flex;
		align-items: center;
		border: 1px solid $gray-300;
		border-radius: 0.2rem;
		height: 4rem;
		padding: 0 1.5rem 0 1rem;
	}
}
.checkBox {
	> input[type='checkBox'] {
		position: absolute;
		left: -9999px;
		&:checked + label {
			&:before {
				opacity: 0;
			}
			&:after {
				opacity: 1;
				transform: scale(1);
			}
		}
		&:disabled + label {
			&:before {
				background: url(./../images/common/icon-check-disable.svg) no-repeat center;
				opacity: 1;
			}
			&:after {
				opacity: 0;
				transform: scale(0);
			}
		}
		&:checked:disabled + label {
			&:before {
				opacity: 0;
			}
			&:after {
				opacity: 0.4;
				transform: scale(1);
			}
		}
	}
	> label {
		position: relative;
		display: flex;
		align-items: center;
		gap: 1rem;
		height: 2.4rem;
		&:before,
		&:after {
			content: '';
			display: block;
			width: 2.4rem;
			height: 2.4rem;
			left: 0;
			top: 0;
			transition: all 0.3s;
		}
		&:before {
			position: relative;
			background: url(./../images/common/icon-check.svg) no-repeat center;
			background-size: 2.4rem;
			opacity: 1;
		}
		&:after {
			position: absolute;
			background: url(./../images/common/icon-check-active.svg) no-repeat center;
			background-size: 2.4rem;
			opacity: 0;
			transform: scale(0);
		}
		> span {
			margin-bottom: 0.1rem;
		}
	}
}

/* Textarea */
textarea {
	width: 100%;
	border: 1px solid $gray-300;
	font-size: 1.6rem;
	text-align: left;
	border-radius: 0.6rem;
	padding: 1rem 1.5rem;
	&:disabled {
		background-color: $gray-50;
		color: $gray-400;
	}
	&::-webkit-input-placeholder {
		color: $gray-500;
	}
	&::-moz-placeholder {
		color: $gray-500;
	}
	&:-ms-input-placeholder {
		color: $gray-500;
	}
	&::-ms-input-placeholder {
		color: $gray-500;
	}
	&::placeholder {
		color: $gray-500;
	}
	&:hover {
		border: 1px solid $gray-600;
	}
}
.textarea {
	label {
		display: block;
	}
}

/* Mobile */
.mobile-set {
	display: flex;
	gap: 1.6rem;
	width: 32rem;
	> li {
		position: relative;
		width: 100%;
		&:not(:first-child):before {
			content: '';
			position: absolute;
			display: block;
			width: 0.6rem;
			height: 0.15rem;
			left: -1.1rem;
			top: 50%;
			background-color: $gray-800;
			z-index: 1;
		}
	}
}

/* Address */
.address-set {
	display: flex;
	flex-direction: column;
	gap: 0.8rem;
	.address-number {
		display: flex;
		gap: 0.8rem;
		width: 29.7rem;
		> button {
			flex: none;
		}
	}
}

/* Scroll Bar */
.scrollBar::-webkit-scrollbar {
	width: 0.8rem; /*   */
}
.scrollBar::-webkit-scrollbar-thumb {
	height: 25%; /*   */
	background: #e7e4e3; /*   */
	border-radius: 0.4rem;
}
.scrollBar::-webkit-scrollbar-track {
	background: transparent; /*   */
}

.round-box {
	overflow: hidden;
	margin-top: 4rem;
	&.img img {
		margin-top: 4rem;
		border-radius: 1.2rem;
	}
	&__item {
		padding: 3.2rem;
		background-color: $gray-50;
		border-radius: 1rem;
		dl {
			& + dl {
				margin-top: 2.8rem;
			}
			dt {
				font-size: 1.8rem;
				margin-bottom: 0.8rem;
			}
			dd,
			dd span {
				font-size: 1.8rem;
				font-weight: 400;
				color: $gray-700;
			}
			dd span {
				vertical-align: middle;
				&::before {
					margin: 0 1rem;
					display: inline-block;
					vertical-align: middle;
					width: 0.5rem;
					height: 0.5rem;
					border-radius: 50%;
					background-color: $gray-400;
					content: '';
				}
			}
		}
		p {
			position: relative;
			margin-top: 2.8rem;
			padding-left: 2.4rem;
			color: $gray-600;
			font-weight: 400;
			&::before {
				position: absolute;
				left: 0;
				top: 1px;
				width: 2rem;
				height: 2rem;
				background: url(./../images/common/icon-info.svg) no-repeat center;
				content: '';
			}
			span {
				color: $gray-700;
			}
		}
	}
	.checkBox {
		display: flex;
		justify-content: flex-end;
		padding: 2.2rem 3.2rem;
	}
}

/* Select  */
select {
	color: $gray-600;
	width: 100%;
	height: 4rem;
	line-height: 4rem;
	border: 1px solid $gray-300;
	background: $white url(./../images/common/icon-arrow-down-20.svg) no-repeat center right 0.8rem;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	font-size: $normalSize;
	padding-left: 1.6rem;
	padding-right: 4.6rem;
	border-radius: 0.6rem;
	&::-ms-expand {
		display: none;
	}
	&:hover {
		border: 1px solid $gray-600;
	}
	&:disabled {
		color: $gray-400;
		background-color: $gray-50;
		background: $gray-50 url(./../images/common/icon-arrow-down-20.svg) no-repeat center right 0.8rem;
		&:hover {
			border: 1px solid $gray-300;
		}
	}
	&.color-blue {
		outline: none;
		font-size: 1.8rem;
		font-weight: 700;
		padding-left: 2rem;
		height: 5.6rem;
		border-radius: 0.6rem;
		border-color: $primary-600;
		color: $white;
		background: $primary-600 url(./../images/common/icon-arrow-down-20-white.svg) no-repeat center right 1.6rem;
		option {
			background-color: $white;
			color: $gray-600;
		}
	}
}
// Select 
.select {
	display: block;
	position: relative;
	.selected {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 4rem;
		padding-left: 1.6rem;
		padding-right: 4.6rem;
		border: 1px solid $gray-300;
		&:hover {
			border-color: $gray-600;
		}
		.selected-value {
			font-size: $normalSize;
			white-space: nowrap;
			/*   */
			overflow: hidden;
			text-overflow: ellipsis;
			/*   */
			color: $gray-900;
		}
		&:after {
			content: '';
			position: absolute;
			display: block;
			width: 2.4rem;
			height: 2.4rem;
			right: 1rem;
			top: calc(50% - 1.2rem);
			background: url(./../images/common/icon_select_arrow.svg) no-repeat center;
			transition: all 0.3s;
		}
	}
	&.active {
		z-index: 1;
		.selected {
			border-color: $gray-600;
			&:after {
				transform: rotate(180deg);
			}
		}
	}
	&.default {
		.selected {
			.selected-value {
				color: $gray-500;
			}
		}
	}
	ul {
		position: absolute;
		display: none;
		width: 100%;
		border: 1px solid $gray-300;
		background: $white;
		border-top: none;
		cursor: pointer;
		li {
			white-space: nowrap;
			/*   */
			overflow: hidden;
			text-overflow: ellipsis;
			/*   */
			padding: 0 1.6rem;
			height: 3.6rem;
			line-height: 3.6rem;
			transition: all 0.3s;
		}
	}
	&.active {
		ul {
			display: initial;
			li {
				&:hover {
					background-color: $gray-50;
				}
			}
		}
	}
}

/* Table */

table {
	width: 100%;

	&.th-color th {
		background-color: $gray-10;
	}

	tr {
		&.bg-color {
			th,
			td {
				background-color: $gray-10;
			}
		}
	}
}

.type-ta {
	border-top: 0.1rem solid $gray-700;
	border-bottom: 1px solid $gray-200;
	th {
		height: 6rem;
		padding: 1.2rem 0;
		font-size: 1.8rem;
		font-weight: 600;
		label > span {
			color: $primary-400;
		}
	}
	td {
		height: 6rem;
		padding: 1.2rem 2rem 1.2rem 4rem;
		font-size: 1.6rem;
		font-weight: 400;
		& > div {
			& + div {
				margin-top: 0.8rem;
			}
			&.checkBox {
				& + .checkBox {
					margin-top: 1.6rem;
				}
			}
		}
	}
	tr:first-child th,
	tr:first-child td {
		padding-top: 3.2rem;
	}
	tr:last-child th,
	tr:last-child td {
		padding-bottom: 3.2rem;
	}
	.align-top {
		vertical-align: top;
	}
	input[type='text'],
	input[type='password'],
	select {
		width: 32rem;
		height: 4.8rem;

		&.sm {
			width: 12rem;
		}

		&.full {
			width: 75rem;
		}
	}
	textarea {
		width: 60rem;
	}
	.mobile-set select {
		width: 100%;
	}
	&.text {
		border-top: 0;
		border-bottom: 0;
		tr:first-child th,
		tr:first-child td {
			padding-top: 4rem;
		}
		th,
		td {
			padding: 3rem 0;
		}
		th {
			font-size: 2.4rem;
			font-weight: 600;
			width: 26rem;
		}
	}
	th,
	td {
		height: 6rem;
		&.bg-point {
			background-color: $gray-50;
			border-radius: 1rem;
			padding: 3.2rem;
		}
		p {
			&.point-circle,
			&.point-notice {
				padding-left: 2.4rem;
				font-weight: 400;
				&:first-child {
					margin-top: 0;
				}
			}
			&.point-circle {
				position: relative;
				font-size: 2rem;
				margin-top: 1.2rem;
				&:before {
					content: '';
					display: inline-block;
					position: absolute;
					width: 0.5rem;
					height: 0.5rem;
					top: 0.95rem;
					left: 0.95rem;
					border-radius: 2rem;
					background-color: $gray-500;
				}
			}
			&.point-notice {
				margin-top: 0.8rem;
				font-size: 1.6rem;
				color: $gray-700;
			}
		}
	}
}
.type-gr {
	border-top: 0.1rem solid $gray-700;
	&.mt-40 {
		margin-top: 4rem;
	}
	&.mt-60 {
		margin-top: 6rem;
	}
	th,
	td {
		height: 6rem;
		padding: 2.2rem 2.4rem;
		border-bottom: 0.1rem solid $gray-200;
		&.text-md {
			font-size: 1.8rem;
		}
	}
	&.searchResult {
		th,
		td {
			height: 16.2rem;
			padding: 2.8rem 0 3.8rem;
		}
	}
}
.pagenation {
	display: flex;
	justify-content: center;
	gap: 1rem;
	margin-top: 4rem;
	li {
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 4rem;
			height: 4rem;
			text-decoration: none;
			border-radius: 50%;
			color: $gray-500;
			&.page-prev {
				border: 1px solid $gray-300;
				font-size: 0;
				background: url(./../images/common/icon-arrow-left-20.svg) no-repeat center;
			}
			&.page-next {
				border: 1px solid $gray-300;
				font-size: 0;
				background: url(./../images/common/icon-arrow-right-20.svg) no-repeat center;
			}
		}
		&.active {
			a {
				background-color: $primary-500;
				color: $white;
			}
		}
	}
}

/* paging-button-area */
.paging-button-area {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 4rem;

	> button {
		position: absolute;
		right: 0;
	}

	.pagenation {
		margin-top: 0;
	}
}

.table-sm {
	margin-top: 4rem;
	padding: 0 27.5rem;

	button {
		margin: 4rem auto;
	}

	.table-wrap {
		margin-top: 1.6rem;

		& .table-txt {
			width: 70%;
		}
	}

	.table-txt {
		p {
			font-size: 1.8rem;

			/*  1 */
			overflow: hidden;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
		}
	}

	.type-gr {
		tr {
			flex-direction: unset;
			border-bottom: 1px solid $gray-200;
		}

		td {
			padding: 3.2rem 0;
			border-bottom: none;
		}

		.td-wrap {
			justify-content: start;
			gap: 2rem;

			.checkBox {
				padding-right: 0.8rem;
			}
		}
	}

	td {
		padding: 3.2rem 0;

		& div {
			display: flex;
			align-items: center;
		}

		&:first-child {
			padding-right: 6.5rem;
		}
	}

	.table-ul {
		display: flex;
		align-items: center;
		justify-content: end;

		.tag {
			min-width: 7rem;
		}
	}
}

.table-txt {
	display: flex;
	gap: 2rem;
	align-items: center;
	a {
		text-decoration: none;
	}
	p {
		line-height: 1.6;
		&.tit {
			font-size: 1.8rem;
			/* 2 */
			overflow: hidden;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
		}
	}
	ul {
		li,
		span {
			color: $gray-600;
		}
		span {
			position: relative;
			margin-right: 1.2rem;
			&:after {
				content: '';
				display: block;
				width: 1px;
				height: 1.2rem;
				position: absolute;
				right: -0.8rem;
				top: 50%;
				transform: translateY(-50%);
				background-color: $gray-400;
			}
		}
	}
	&-breadcrumb {
		margin-bottom: 0.8rem;
		> strong {
			color: $gray-700;
			font-size: 1.6rem;
			line-height: 2.4rem;
			+ strong {
				padding-left: 2.4rem;
				background: url('./../images/common/icon_arrow-left.svg') no-repeat center left;
			}
		}
	}
	&-info {
		margin-top: 0.8rem;
		> span {
			color: $gray-500;
			font-size: 1.6rem;
			font-weight: 400;
			line-height: 2rem;
			position: relative;
			+ span {
				padding-left: 2.5rem;
				&::before {
					content: '';
					width: 1px;
					height: 1.2rem;
					background-color: $gray-300;
					position: absolute;
					left: 1.2rem;
					top: 50%;
					transform: translateY(-50%);
				}
			}
		}
	}
}
.table-buttons {
	display: flex;
	justify-content: flex-end;
	gap: 0.8rem;
	align-items: center;
}
/* table-ul PC */
.table-ul {
	display: block;
	text-align: right;
	li {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		text-align: left;
		&:not(:first-child) {
			margin-left: 1.4rem;
		}
		&:not(:first-child):after {
			content: '';
			width: 1px;
			height: 1.2rem;
			display: block;
			position: absolute;
			left: -0.8rem;
			top: 50%;
			transform: translateY(-50%);
			background-color: $gray-400;
		}
		&:nth-last-child(2) {
			width: 8.5rem;
		}
		&:nth-last-child(2):after {
			display: none;
		}
		&:last-child {
			width: 10rem;
		}
		span {
			color: $gray-600;
			font-weight: 400;
		}
		.lock {
			display: inline-block;
			vertical-align: middle;
		}
		.lock + .tag {
			margin-left: 1.5rem;
			display: inline-block;
			vertical-align: middle;
			line-height: 3.2rem;
		}
	}
}
.fixed-pin {
	display: inline-block;
	width: 2.4rem;
	height: 2.4rem;
	padding: 0;
	border: none;
	background: url(./../images/common/icon_pin.svg) no-repeat center;
}
.btn-link {
	display: block;
	width: 2.4rem;
	height: 2.4rem;
	padding: 0;
	border: none;
	background: url(./../images/common/icon_link.svg) no-repeat center;
}
.added-file {
	width: 3.6rem;
	height: 3.6rem;
	padding: 0;
	border: none;
	background: url(./../images/common/icon-addFile.svg) no-repeat center;
}
.icon-eye {
	width: 3.6rem;
	height: 3.6rem;
	padding: 0 0 0 2.4rem;
	border: none;
	background: url(./../images/common/icon-eye.svg) no-repeat center left;
}
.lock {
	width: 2.4rem;
	height: 2.4rem;
	margin-left: -1rem;
	flex: none;
	background: url(./../images/common/icon-lock.svg) no-repeat center;
}

/* Tag */
.tag {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 3.2rem;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	min-width: 5.1rem;
	border-radius: 0.6rem;
	color: $white;
	padding: 0 1rem;
	flex: none;
	font-size: 1.8rem;
	&.normal-100 {
		background-color: $gray-100;
		color: $gray-800;
	}
	&.greenblue-100 {
		background-color: $greenblue-200;
		color: $greenblue-100;
	}
	&.green-100 {
		background-color: $green-200;
		color: $green-100;
	}
	&.orange-100 {
		background-color: $yellow-200;
		color: $yellow-100;
	}
	&.purple-100 {
		background-color: $purple-200;
		color: $purple-100;
	}
	&.blue-100 {
		background-color: $blue-200;
		color: $blue-100;
	}
	&.red-100 {
		background-color: $red-200;
		color: $red-100;
	}
	&.brown-100 {
		background-color: $brown-200;
		color: $brown-100;
	}

	&.blue-100-r {
		background-color: $white;
		border: 1px solid $primary-200;
		color: $blue-100;
		border-radius: 1.6rem;
	}
	&.gray-100-r {
		background-color: $gray-50;
		border: 1px solid $gray-50;
		color: $gray-500;
		border-radius: 1.6rem;
	}
	&.primary-600 {
		background-color: $primary-50;
		color: $primary-600;
	}
}

/* Sub  */
.sub {
	.content-area {
		display: flex;
		flex-direction: column;
		align-items: center;
		> section {
			min-width: 1320px;
			max-width: 1320px;
			padding: 0 2rem;
			margin: 0 auto;
		}
		> section {
			/*    */
			.title-area {
				padding-top: 12rem;
				&.type-01 {
					padding-bottom: 4rem;
					border-bottom: 1px solid $gray-700;
				}
			}
			&:last-child {
				padding-bottom: 12rem;
			}
		}
	}
	.data-form {
		width: 100%;
	}
	/* data-table-area PC */
	.data-table-area {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		flex-direction: row;
		.total-ea {
			font-size: 1.6rem;
			font-weight: 400;
		}
		.data-table {
			margin-top: 4rem;
			.sort-area {
				display: flex;
				gap: 0.8rem;

				> div {
					display: flex;
					gap: 0.8rem;
				}

				.selectBox {
					width: 12rem;
				}
				.searchText {
					width: 24rem;
				}
			}
		}
	}
}

/*  */
.list-details {
	border-top: 1px solid $gray-700;
	> .list-title-area {
		position: relative;
		padding: 3.2rem;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		border-bottom: 1px solid $gray-200;
		.tag {
			margin-bottom: 1.2rem;
		}
		.title {
			font-size: 2.4rem;
			line-height: 3.2rem;
			font-weight: 600;
		}
		> .table-ul {
			flex: 0 0 auto;
			margin-left: 8rem;
			li:nth-child(2):after {
				display: block;
			}
			li .lock {
				margin-top: -0.3rem;
				margin-right: 1.8rem;
			}
		}
		&.qna {
			border-bottom: 1px solid $gray-100;
			.tag-area {
				display: flex;
				gap: 0.8rem;
			}
			.tit-area {
				position: relative;
				display: flex;
				align-items: center;
				gap: 0.5rem;
				> i {
					width: 4rem;
					height: 4rem;
					background-size: 4rem;
					margin-left: 0;
					margin-bottom: 0.4rem;
				}
			}
		}
	}
	> .added-file-area {
		background-color: $gray-50;
		padding: 2.4rem;
		border-top: 1px solid $gray-100;
		border-bottom: 1px solid $gray-100;
		border-radius: 1rem;
		margin: 1.6rem 3.2rem 0;
		li {
			padding-left: 2.8rem;
		}
		li:not(:first-child) {
			margin-top: 1.4rem;
		}
		a {
			position: relative;
			color: $gray-700;
			font-weight: 400;
			text-decoration: none;
			&:hover {
				color: $gray-800;
			}
			&:before {
				content: '';
				position: absolute;
				display: block;
				width: 2.4rem;
				height: 2.4rem;
				left: -2.8rem;
				top: -0.2rem;
				background: url(./../images/common/icon-addFile.svg) no-repeat center;
			}
			&:hover:after {
				content: '';
				position: absolute;
				display: block;
				width: 100%;
				height: 0.1rem;
				background-color: $gray-900;
				bottom: -1px;
			}
		}
	}
	> .contents {
		padding: 6rem 3.2rem;
		font-size: 1.8rem;
		font-weight: 400;
		line-height: 1.4;
	}
	&.answer {
		border-top: none;
		border-bottom: 1px solid $gray-300;
		> .title-area {
			background-color: $gray-500;
			padding: 4rem 4.8rem;
			> .table-ul {
				right: 4.8rem;
			}
		}
		> .contents {
			background-color: $gray-500;
			padding: 6rem 4.8rem 6rem;
		}
	}
	& + .bottom-area {
		padding-top: 4rem;
		margin-top: 0;
		border-top: 1px solid $gray-200;
	}

	.list-item {
		background-color: $gray-10;
		li {
			display: flex;
			align-items: center;
			padding: 2.4rem;
			& + li {
				border-top: 1px solid $gray-200;
			}
		}
		&__no {
			margin-right: 1.2rem;
			display: inline-block;
			max-width: 4.2rem;
			width: 100%;
			text-align: center;
			font-size: 1.8rem;
			font-weight: 400;
			line-height: 2.4rem;
			color: $gray-700;
		}
		&__name {
			flex: 1;
			font-size: 2rem;
			font-weight: 400;
			line-height: 2.4rem;
			color: $gray-800;
		}
		.ico-download {
			padding: 1.4rem;
			height: 4.8rem;
			min-width: 12rem;
			font-size: 1.6rem;
			font-weight: 500;
			color: $gray-800;
			span {
				margin-right: 1rem;
			}

			&::after {
				display: inline-block;
				width: 2rem;
				height: 2rem;
				background: url(./../images/common/ico-download.svg) no-repeat center;
				content: '';
			}
		}
	}
}
.list-move {
	> li {
		height: 7.6rem;
		border-bottom: 1px solid $gray-200;
		display: flex;
		align-items: center;
		padding: 0 3.2rem;
		.btn-list-next,
		.btn-list-before {
			height: 2.8rem;
			border: none;
			padding-left: 2rem;
			padding-right: 2.4rem;
			flex: 0 0 auto;
			span {
				font-size: 1.6rem;
				font-weight: 400;
				color: $gray-700;
			}
		}
		&:first-child {
			border-top: 1px solid $gray-200;
		}
		.btn-list-next {
			background: url(./../images/common/icon-arrow-up.svg) no-repeat center left;
		}
		.btn-list-before {
			background: url(./../images/common/icon-arrow-down.svg) no-repeat center left;
		}
		a {
			text-decoration: none;
			flex: 1 1 auto;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;
			p {
				font-size: 1.8rem;
				display: inline;
				&.empty-contents {
					color: $gray-600;
				}
			}
		}
		.date {
			color: $gray-600;
			font-weight: 400;
			margin-left: 4rem;
		}
	}
}

.bottom-area {
	margin-top: 4rem;
	display: flex;
	justify-content: center;
	&.buttons {
		gap: 0.8rem;
	}
}

/* PDF */
.pdf-details {
	display: flex;
	border-top: 1px solid $black;
	padding-top: 4rem;
	.pdf-view {
		width: 100%;
		height: 105.9rem;
		background-color: $gray-900;
		margin-right: 5.2rem;
	}
	.pdf-control {
		flex: none;
		width: 32rem;
		.pdt-btn-area {
			margin-bottom: 4rem;
			> button {
				width: 100%;
				height: 5.6rem;
				justify-content: space-between;
				+ button {
					margin-top: 1.2rem;
				}
				i {
					display: block;
					width: 3.2rem;
					height: 3.2rem;
					&.search-plus {
						background: url(./../images/common/icon-search-plus.svg) no-repeat center;
					}
					&.download {
						background: url(./../images/common/icon-download-black.svg) no-repeat center;
					}
				}
			}
		}
		.pdf-page-select {
			margin-top: 0.8rem;
			> ul {
				border: 1px solid $gray-300;
				border-radius: 0.6rem;
				overflow: hidden;
				> li {
					border-bottom: 1px solid $gray-100;
					> button {
						position: relative;
						width: 100%;
						height: 5.2rem;
						justify-content: flex-start;
						border: none;
						border-radius: 0;
						padding-left: 2rem;
						&.active,
						&:hover {
							background-color: $primary-10;
							color: $primary-500;
							&:after {
								content: '';
								width: 3px;
								height: 100%;
								position: absolute;
								display: inline-block;
								top: 0;
								left: 0;
								background-color: $primary-500;
							}
						}
					}
				}
			}
		}
	}
}

/* GNB */
.scroll {
	overflow: hidden;
}
.gnb-wrap {
	z-index: 100;
	position: fixed;
	display: block;
	right: 0;
	top: 0;
	width: 90%;
	height: 100%;
	background: $white;
	&.active,
	&.slideBack {
		display: block;
		transition: all 0.7s;
	}
	h1 {
		z-index: 1;
		position: absolute;
		display: block;
		left: 3.7rem;
		top: 4rem;
		width: 23rem;
		height: 2.8rem;
		font-size: 0;
		background: url(./../images/common/logo-search-pc.svg) no-repeat center;
		background-size: 23rem 2.8rem;
	}
	.btn-menu-close {
		display: block;
		position: absolute;
		right: 8.3rem;
		top: 3rem;
		width: 4.8rem;
		height: 4.8rem;
		padding: 0;
		border: none;
		background: url(./../images/common/btn-close.svg) no-repeat center;
		transition: all 0.3s;
		&:hover {
			transform: rotate(180deg);
		}
	}
	.depth1 {
		position: relative;
		background-color: #fafafa;
		height: 100%;
		width: 42rem;
		padding-top: 13rem;
		> li {
			position: relative;
			display: flex;
			justify-content: flex-end;
			> a,
			span {
				cursor: pointer;
				display: flex;
				height: 6.8rem;
				font-size: 3.2rem;
				font-weight: 700;
				padding-right: 6rem;
				color: $gray-800;
				opacity: 0.2;
				text-decoration: none;
				transition: all 0.2s;
			}
			&:hover,
			&.on {
				> a,
				span {
					color: $red-100;
					opacity: 1;
				}
				.depth2 {
					display: flex;
					transform: scaleX(1);
					opacity: 1;
					visibility: visible;
				}
			}
			&:after {
				content: '';
				position: absolute;
				width: 0.4rem;
				height: 2.8rem;
				display: block;
				top: 0.8rem;
				right: 0;
				background-color: $red-100;
				transform: scaleY(0);
				transition: all 0.3s;
			}
			&:hover:after,
			&.on:after {
				transform: scaleY(1);
			}
			.depth2 {
				position: absolute;
				display: flex;
				flex-wrap: wrap;
				top: 0.4rem;
				left: 42rem;
				width: calc(100vw - 46rem);
				transform-origin: left;
				opacity: 0;
				transform: scaleX(0);
				visibility: hidden;
				transition: all 0.3s;
				> li {
					cursor: pointer;
					padding-top: 0.5rem;
					padding-left: 3rem;
					padding-bottom: 1rem;
					> a {
						position: relative;
						display: inline-block;
						text-decoration: none;
						color: $gray-800;
						font-size: 2rem;
						line-height: 2.6rem;
						font-weight: 700;
						opacity: 0.2;
						transition: all 0.3s;
						&:before {
							content: '';
							position: absolute;
							display: block;
							width: 100%;
							height: 0.2rem;
							background-color: $red-100;
							bottom: -0.1rem;
							transform: scaleX(0);
							transition: all 0.3s;
						}
					}
					&:hover,
					&.on {
						> a {
							color: $red-100;
							opacity: 1;
							&:before {
								transform: scaleX(1);
							}
						}
					}
				}
			}
		}
	}
}

