			}
		}
	}

	.mo-dsp-none {
		display: none;
	}

	/*  */
	.list-details {
		> .list-title-area {
			display: block;
			position: relative;
			padding: 2rem 1.6rem;
			.tag {
				margin-bottom: 1.6rem;
			}
			.title {
				font-size: 1.8rem;
				line-height: 2.4rem;
				font-weight: 600;
			}
			> .table-ul {
				margin-left: 0;
				margin-top: 0.4rem;
			}
			&.qna {
				padding-bottom: 5.2rem;
				> .table-ul {
					bottom: 2.2rem;
				}
				.tag-area {
					gap: 0.4rem;
				}
				.tit-area {
					> i {
						position: absolute;
						width: 2.4rem;
						height: 2.4rem;
						background-size: 2.4rem;
						margin-left: 0;
						margin-bottom: 0;
						top: -3.8rem;
						right: 0;
					}
				}
			}
		}
		> .added-file-area {
			margin: 1.6rem 0 0;
			padding: 2rem 1.6rem;
			li {
				min-height: 2.4rem;
				/* 2 */
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}
			li:not(:first-child) {
				margin-top: 1rem;
			}
			a {
				font-size: 1.4rem;
			}
		}
		> .contents {
			padding: 4rem 1.6rem;
			font-size: 1.4rem;
			font-weight: 400;
			line-height: 1.4;
		}
		&.answer {
			border-top: none;
			border-bottom: 1px solid $gray-300;
			> .title-area {
				padding: 2.8rem 2rem 4.8rem;
				> .table-ul {
					right: 4.8rem;
				}
			}
			> .contents {
				background-color: $white;
				padding: 4rem 2rem 4rem;
			}
		}

		.list-item {
			li {
				padding: 2rem 1.6rem;
			}
			&__name {
				font-size: 1.6rem;
				font-weight: 500;
				line-height: 2rem;
			}
			.ico-download {
				min-width: auto;
			}
		}
	}
	.list-move {
		> li {
			height: 6.4rem;
			padding: 0 1.6rem;
			.btn-list-next,
			.btn-list-before {
				height: 2rem;
				padding-right: 1.6rem;
			}
			a {
				p {
					font-size: 1.6rem;
				}
			}
			.date {
				display: none;
			}
		}
	}
	.bottom-area {
		margin-top: 2.4rem;
		> .btn-m {
			width: 100%;
			height: 4.8rem;
			min-width: inherit;
			> span {
				font-size: 1.6rem;
				font-weight: 500;
			}
		}
	}

	/* PDF */
	.pdf-details {
		flex-direction: column-reverse;
		padding-top: 2.4rem;
		.pdf-view {
			width: 100%;
			height: 96rem;
			margin-top: 4rem;
		}
		.pdf-control {
			width: 100%;
			.pdt-btn-area {
				display: flex;
				margin-bottom: 0.8rem;
				> button {
					width: 100%;
					height: 4.8rem;
					+ button {
						margin-top: 0;
						margin-left: 0.8rem;
					}
				}
			}
			.pdf-page-select {
				display: none;
			}
		}
	}

	select {
		&.color-blue {
			height: 4.8rem;
			padding-left: 1.6rem;
		}
	}

	/* GNB */
	/*  GNB */
	.gnb-wrap {
		h1 {
			left: 0;
			top: 0;
			width: 100%;
			height: 6.4rem;
			border-bottom: 1px solid $gray-100;
			background: #fff url(./../images/common/logo-search-pc.svg) no-repeat left 2rem center;
			background-size: 19rem;
		}
		.btn-menu-close {
			z-index: 1;
			right: 2rem;
			top: 2.2rem;
			width: 2rem;
			height: 2rem;
			background: url(./../images/common/btn-close.svg) no-repeat center;
		}
		.depth1 {
			width: 16rem;
			padding-top: 9rem;
			border-right: 1px solid $gray-100;
			> li {
				position: relative;
				display: flex;
				justify-content: flex-start;
				> a,
				span {
					height: 4.4rem;
					font-size: 2.4rem;
					font-weight: 700;
					padding-left: 3rem;
					padding-right: inherit;
					color: $gray-800;
					opacity: 0.2;
					text-decoration: none;
					transition: all 0.2s;
				}
				&:hover,
				&.on {
					> a,
					span {
						color: $red-100;
						opacity: 1;
					}
					.depth2 {
						display: block;
						transform: scaleX(1);
						opacity: 1;
						visibility: visible;
					}
				}
				&:after {
					content: '';
					position: absolute;
					width: 0.4rem;
					height: 2rem;
					display: block;
					top: 0.8rem;
					right: 0;
					background-color: $red-100;
					transform: scaleY(0);
					transition: all 0.3s;
				}
				&:hover:after {
					transform: scaleY(1);
				}
				.depth2 {
					position: absolute;
					display: block;
					top: 0.3rem;
					left: 16rem;
					width: calc(100vw - 16rem);
					opacity: 0;
					transform-origin: left;
					transform: scaleX(0);
					visibility: hidden;
					transition: all 0.3s;
					> li {
						cursor: pointer;
						padding-left: 3rem;
						padding-bottom: 1rem;
						> a {
							position: relative;
							display: inline-block;
							text-decoration: none;
							color: $gray-700;
							font-size: 1.6rem;
							font-weight: 500;
							line-height: 2.2rem;
							opacity: 1;
							transition: all 0.3s;
							&:before {
								content: '';
								position: absolute;
								display: block;
								width: 100%;
								height: 0.2rem;
								background-color: $red-100;
								bottom: -0.1rem;
								transform: scaleX(0);
								transition: all 0.3s;
							}
						}
						&:hover {
							> a {
								color: $red-100;
								opacity: 1;
								&:before {
									transform: scaleX(1);
								}
							}
						}
					}
				}
			}
		}
	}
	/*  GNB END */

	.mo-only {
		display: flex;
	}
	.pc-only {
		display: none !important;
	}
	.lock {
		position: absolute;
		right: 4rem;
	}

	/* Scroll Bar */
	.scrollBar::-webkit-scrollbar {
		width: 0.6rem; /*   */
	}
	.scrollBar::-webkit-scrollbar-thumb {
		border-radius: 0.3rem;
	}
	/* Round Box */
	.round-box {
		&.pdf {
			margin-top: 2.4rem;
		}
		> .round-box-top {
			padding: 1rem 1rem 1rem 0;
			p {
				font-size: 1.4rem;
				line-height: 1.4;
				&:first-child {
					margin-bottom: 1rem;
				}
			}
			> ul {
				height: 25.4rem;
				overflow-y: auto;
				padding: 1rem 1rem 1rem 2rem;
			}
		}
		> .round-box-bottom {
			padding: 1.2rem 2rem;
		}
		.checkBox {
			justify-content: center;
		}
	}
	.type-ta {
		.w-24 {
			width: 100% !important;
		}
	}

	/* PDF */
	.pdf-area {
		flex-direction: column;
		margin-top: 1.6rem;
		border-top: 1px solid $gray-700;

		> li {
			width: 100%;
			margin-top: 0;
			padding: 2rem 3rem;

			&:first-child {
				margin-top: 2.4rem;
				padding-top: 0;
			}

			&:last-child {
				margin-bottom: 2.4rem;
				padding-bottom: 0;
			}

			> div {
				gap: 1.2rem;
				width: 100%;

				p {
					font-size: 1.8rem;
					margin-bottom: 0;
					text-align: center;
				}

				& .img-wrap {
					button {
						width: calc(100% - 4rem);
						height: 4.8rem;

						span {
							font-size: 1.6rem;
						}
					}
				}
			}
		}
	}

	.card-area {
		display: block;
		a {
			width: 100%;
			height: 20rem;

			.card-title {
				font-size: 2.4rem;
			}

			.card-bottom {
				margin-top: 1.6rem;
				font-size: 1.8rem;
			}

			+ a {
				margin-left: 0;
				margin-top: 1.2rem;
			}
		}
	}

	/*  */
	.mov-area {
		flex-direction: column;
		margin-top: 1.6rem;
		border-top: 1px solid $gray-700;

		&:last-child::after {
			display: none;
		}

		> li {
			width: 100%;

			&:first-child {
				margin-top: 2.4rem;
			}
			&:last-child {
				margin-bottom: 0;
			}
			> div,
			.popModalShow {
				i.icon-movie {
					top: 40%;
				}

				p {
					line-height: 2.4rem;
					height: 2.4rem;

					/*  */
					overflow: hidden;
					text-overflow: ellipsis;
					-webkit-line-clamp: 1;
				}

				ul {
					li {
						span {
							font-size: 1.4rem;
						}
					}
				}
			}
		}
	}
	.txt-tip {
		font-size: 1.4rem;
		margin-top: 0.5rem;
		line-height: 1.4;
		padding-top: 0.32rem;
	}

	/* high-list */
	.high-list {
		margin-top: 1.2rem;
		> li {
			flex-direction: column;
			padding: 2rem;
			> .con-area {
				width: 100%;
			}
			> .btn-area {
				margin-top: 2rem;
				width: 100%;
				> button {
					width: 100%;
				}
			}
			p {
				font-weight: 400;
				font-size: 1.2rem;
				&.tit {
					font-weight: 700;
					font-size: 1.8rem;
					margin-top: 1.6rem;
				}
			}
		}
	}

	/*  */
	.nodata,
	.nodate {
		p {
			font-size: 1.6rem;
		}
		&.hs2100 {
			margin-top: 5rem;
		}
	}

	/*  */
	.email-wrap {
		gap: 1rem;
		width: 100%;
	}

	.date-box.time .inputDate {
		width: 100%;
	}

	/* button */

	.searchRoundText {
		input {
			height: 5.6rem;
			width: 100%;
			padding: 0 6.2rem 0 2.4rem;
			border-radius: 5.6rem;
			color: $black;
			font-size: 1.6rem;
			font-weight: 600;
		}
		button {
			position: absolute;
			display: block;
			right: 2rem;
			width: 3.2rem;
			height: 3.2rem;
			border: none;
			background-size: 3.2rem;
			background-position-x: right;
			&.sr-red {
				background-size: 3.2rem;
				background-position-x: right;
			}
		}
	}
}

/* GNB */
@media (min-width: 801px) and (max-width: 1600px) {
	.gnb-wrap {
		.depth1 {
			width: 30rem;
			> li {
				.depth2 {
					left: 30rem;
				}
			}
		}
	}
}

/* dimmer */
.dimmer {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.6);
}

/*   section */
.searchTotal {
	&-wrap {
		display: none;
		opacity: 0;
		height: 0;
		-webkit-animation: 0.6s ease 0s accordionDown;
		animation: 0.6s ease 0s accordionDown;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		min-width: 1320px;
		max-width: 100%;

		&.active {
			display: flex;
			opacity: 1;
			height: 58.4rem;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			background-color: $white;
			z-index: 100;
		}
		h1 {
			position: absolute;
			top: 3.5rem;
			left: 0.8rem;
			background-image: url('./../images/common/logo-search-pc.svg');
		}

		.btn-searchTotal-close {
			display: block;
			position: absolute;
			right: 8.3rem;
			top: 3rem;
			width: 4.8rem;
			height: 4.8rem;
			padding: 0;
			border: none;
			background: url(./../images/common/btn-close.svg) no-repeat center;
			transition: all 0.3s;
			&:hover {
				transform: rotate(180deg);
			}
		}
	}
	&-container {
		margin-top: 6rem;
		width: 66rem;
		h2 {
			text-align: center;
			font-size: 4.4rem;
			line-height: 4.8rem;
		}
	}
	&-contents {
		margin-top: 4.6rem;
		.chip-area {
			margin-top: 1rem;
			text-align: center;
		}
		.searchRoundText {
			margin-top: 3.4rem;
		}
		.infoText {
			margin-top: 1.6rem;
			> span {
				padding-left: 2.4rem;
				display: inline-block;
				color: #9d9d9d;
				font-size: 1.6rem;
				line-height: 2rem;
				letter-spacing: -0.4px;
				background: url('./../images/common/icon_info_gray.svg') no-repeat left 2px;
			}
		}
	}
	&-recommend-words {
		.tit {
			color: $gray-900;
			font-size: 1.8rem;
			line-height: 2.8rem;
			text-align: center;
		}
	}
}

@-webkit-keyframes accordionDown {
	0% {
		opacity: 0;
		height: 0;
	}
	100% {
		opacity: 1;
		height: 58.4rem;
	}
}

@keyframes accordionDown {
	0% {
		opacity: 0;
		height: 0;
	}
	100% {
		opacity: 1;
		height: 58.4rem;
	}
}

@-webkit-keyframes accordionDownMo {
	0% {
		opacity: 0;
		height: 0;
	}
	100% {
		opacity: 1;
		height: 52.8rem;
	}
}

@keyframes accordionDownMo {
	0% {
		opacity: 0;
		height: 0;
	}
	100% {
		opacity: 1;
		height: 52.8rem;
	}
}

/*  mobile */
@media (max-width: 800px) {
	.searchTotal {
		&-wrap {
			-webkit-animation: 0.6s ease 0s accordionDownMo;
			animation: 0.6s ease 0s accordionDownMo;
			&.active {
				width: 100%;
				max-width: none;
				min-width: 100%;
				justify-content: flex-start;
				align-items: flex-start;
				height: 52.8rem;
				h1 {
					top: 1.2rem;
					left: 1.6rem;
					height: 4rem;
					width: 19.6rem;
				}
				.btn-searchTotal-close {
					z-index: 1;
					right: 2rem;
					top: 2.2rem;
					width: 2rem;
					height: 2rem;
					background: url(./../images/common/btn-close.svg) no-repeat center;
				}
			}
		}
		&-container {
			margin-top: 6.6rem;
			padding: 4.8rem 0 8rem;
			width: 100%;
			h2 {
				font-size: 2.8rem;
			}
		}
		&-contents {
			margin-top: 3rem;
			padding: 0 2rem;
			.searchRoundText {
				margin-top: 2.2rem;
				> input {
					height: 5.6rem;
					font-size: 1.6rem;
					line-height: 5.6rem;
					padding: 0 5.8rem 0 2rem;
				}
				button {
					right: 2rem;
					width: 2.8rem;
					height: 2.8rem;
					background-size: 2.8rem;
					background-position-x: right;
				}
			}
			.infoText > span {
				font-size: 1.4rem;
				line-height: 2rem;
			}
		}
		&-recommend-words .tit {
			font-size: 1.6rem;
			line-height: 2.4rem;
		}
	}
}
/*  mobile END */
/*  section END */
