.notify {
	&-popup {
		padding-top: 8rem;
		background-color: #1c2d45;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		&-wrapper {
			display: none;
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 2;
			&.on {
				display: block;
				background-color: $gray-300;
			}
		}
	}
	&-contents {
		flex: 1 1 auto;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		position: relative;
		overflow: hidden;
		h2 {
			flex: none;
			color: $white;
			font-size: 5rem;
			line-height: 5.8rem;
			text-align: center;
		}
		&::before,
		&::after {
			content: '';
			display: block;
			position: absolute;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% auto;
		}
		&::before {
			width: 32.2rem;
			height: 32.2rem;
			top: 2rem;
			left: -5.3rem;
			background-image: url('../images/main/img-flower-bg-white.png');
		}
		&::after {
			width: 55rem;
			height: 27.7rem;
			background-image: url('../images/main/img-ajou-bg-white.png');
			bottom: -1.7rem;
			right: 0;
		}
	}
	&-card {
		&-swiper {
			width: 100%;
			height: 100%;
			&-area {
				position: relative;
				flex: 1 1 auto;
				overflow: hidden;
				width: 100%;
				height: 100%;
				max-width: 171.2rem;
				max-height: 61.6rem;
				margin: 4rem auto 0;
				padding: 0 8rem;
				.swiper {
					&-wrapper {
						overflow: hidden;
					}
					&-slide {
						height: calc((100% - 1.6rem) / 2);
						background: #405165 url('../images/main/img-symbol.png') no-repeat center center/ 12rem 12rem;
						overflow: hidden;
						border-radius: 1.2rem;
						.noti-card {
							width: 100%;
							height: 100%;
							max-width: none;
						}
					}
				}
			}
			&-button {
				&-prev {
					left: var(--swiper-navigation-sides-offset, 10px);
					right: auto;
					background-image: url('../images/common/icon-arrow-left-38-white.svg');
				}
				&-next {
					right: var(--swiper-navigation-sides-offset, 10px);
					left: auto;
					background-image: url('../images/common/icon-arrow-right-38-white.svg');
				}
			}
		}
		&-arrow {
			border: 1px solid $white;
			border-radius: 100%;
			width: 5.6rem;
			height: 5.6rem;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 3.8rem 3.8rem;
			position: absolute;
			top: var(--swiper-navigation-top-offset, 50%);
			margin-top: calc(0px - (var(--swiper-navigation-size) / 2));
			z-index: 10;
			cursor: pointer;
			display: flex;
			align-items: center;
			justify-content: center;
			&.swiper-button-disabled {
				opacity: 0.3;
				cursor: not-allowed;
			}
		}
	}
	&-checkbox-area {
		margin-top: 8rem;
		.checkBox {
			& > label {
				color: $white;
				&::before,
				&::after {
					left: calc(100% + 4px);
				}
			}
		}
	}
}

@media (max-width: 800px) {
	.notify {
		&-contents {
			margin-top: 2rem;
			h2 {
				width: 100%;
				padding-left: 2rem;
				font-size: 2.8rem;
				line-height: 3.6rem;
				text-align: left;
			}
			&::before,
			&::after {
				display: none;
			}
		}
		&-card {
			&-swiper {
				&.swiper {
					overflow-y: auto;
				}
				&-area {
					overflow-y: auto;
					max-width: none;
					max-height: none;
					margin: 2.4rem auto 0;
					padding: 0 0 0 2rem;
					.swiper {
						&-slide {
							background-size: 9rem 9rem;
						}
					}
				}
				&-controller {
					position: absolute;
					top: 0;
					right: 0;
					z-index: 10;
					display: flex;
					align-items: center;
					gap: 1.2rem;
				}
			}
			&-arrow {
				width: 3.6rem;
				height: 3.6rem;
				background-size: 2.4rem 2.4rem;
				position: relative;
				top: auto;
				left: auto;
				right: auto;
				bottom: auto;
				margin-top: 0;
			}
		}
		&-checkbox-area {
			margin: 2rem auto 2.4rem;
			> .checkBox label {
				font-size: 1.4rem;
				&::before {
					width: 2rem;
					height: 2rem;
					background-size: 2rem;
				}
			}
		}
	}
}
