/* main */
.main {
	/* main-body */
	&-body {
		display: flex;
		width: 100%;
		min-width: 1320px;
		overflow: hidden;
		flex-wrap: wrap;
		/* main > tabpage */
		.tab-contents {
			display: none;
			&.active {
				display: block;
			}
		}
		.main-contents {
			flex: 1 1 0;
			overflow: hidden;
		}
	}
	/* left : main-tab-nav */
	&-tab {
		&--nav {
			flex: none;
			width: 30.8rem;
		}
		&-item {
			display: flex;
			&--button {
				flex: 1 1 auto;
				border: none;
				border-radius: 0;
				justify-content: flex-start;
				padding: 4rem 3.6rem;
				strong {
					color: $gray-400;
					font-size: 3.2rem;
					font-weight: 500;
				}
			}
			&.active {
				background-color: #f1f6fb;
				.main-tab-item--button > strong {
					color: $primary-500;
					font-weight: 700;
				}
			}
		}
	}
	/* right : main-contents */
	&-content {
		background-color: #f3f7fd;
		padding: 7.5rem 0 6rem 8rem;
		/* main--tab contents */
		&--area {
			.content-title {
				display: flex;
				width: 100%;
				align-items: center;
				justify-content: space-between;
				padding-right: 8rem;
				> strong {
					display: block;
					color: #1c2d45;
					font-size: 4rem;
					line-height: 5.8rem;
				}
				.viewAll--link {
					display: block;
					text-decoration: none;
					padding-right: 2.4rem;
					background: url('../images/common/icon-plus.svg') no-repeat center right/2.4rem;
					> span {
						color: #666;
						font-size: 2rem;
						line-height: 2.4rem;
					}
				}
			}
			& + .main-content--area {
				margin-top: 7.5rem;
			}
		}
	}
}
/* 0. ~ 4.  */
/*   */
.noti {
	/* swiper */
	&-swiper {
		margin-top: 2.7rem;
		&-controller {
			margin-top: 1.7rem;
			height: 2.4rem;
			max-width: 120rem;
			position: relative;
			display: flex;
			overflow: hidden;
			.swiper-scrollbar.noti-swiper-scrollbar {
				position: relative;
				bottom: 0;
				left: 0;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				margin-right: 2.5rem;
				flex: 1 1 auto;
			}
		}
		&-pagination {
			&.swiper-pagination {
				&-fraction {
					width: auto;
					flex: none;
					margin: 0 1rem;
					color: $gray-600;
					font-size: 2rem;
					line-height: 2.4rem;
					position: relative;
					top: auto;
					left: auto;
					bottom: auto;
					right: auto;
					.swiper-pagination {
						&-current {
							display: inline-block;
							margin-right: 1rem;
							color: $gray-800;
							font-weight: 600;
							font-size: 2rem;
							line-height: 2.4rem;
						}
						&-total {
							display: inline-block;
							margin-left: 1rem;
							color: $gray-600;
							font-size: 2rem;
							line-height: 2.4rem;
						}
					}
				}
			}
		}
		&-arrow {
			position: relative;
			flex: none;
			&::before {
				content: '';
				display: block;
				width: 2.4rem;
				height: 2.4rem;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 2.4rem;
				cursor: pointer;
			}
			&[aria-disabled='true'] {
				&::before {
					opacity: 0.3;
					cursor: not-allowed;
				}
			}
		}
		&-button {
			&-prev::before {
				background-image: url('../images/common/icon-arrow-left-24.svg');
			}
			&-next::before {
				background-image: url('../images/common/icon-arrow-right-24.svg');
			}
		}
	}
	/* card */
	&-card {
		padding: 3.6rem;
		background-color: $white;
		overflow: hidden;
		border-radius: 1.2rem;
		max-width: 41.6rem;
		height: 27rem;
		&--slide {
			position: relative;
			width: auto;
		}
		&--link {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			text-decoration: none;
			width: 100%;
			height: 100%;
		}
		&-title {
			strong {
				display: block;
				color: #333;
				font-size: 2.4rem;
				line-height: 1.333;
				font-weight: 600;
				margin-top: 1.6rem;
				word-break: keep-all;
				word-wrap: break-word;
			}
		}
		&-info {
			flex: none;
			> span {
				font-size: 2rem;
				color: $gray-500;
			}
		}
	}
}

/* 0._ */
.brochure {
	&-swiper {
		margin-top: 2.8rem;
		position: relative;

		&-controller {
			padding: 1rem 0;
		}
	}
	&-slide.swiper-slide {
		height: auto;
		width: auto;
	}
	&-card {
		width: 35rem;
		height: 44rem;
		overflow: hidden;
		border-radius: 1.2rem;
		position: relative;
		> img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		&-hover {
			display: none;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: rgba(0, 0, 0, 0.6);
			padding: 3rem;
			> strong {
				display: block;
				color: $white;
				font-size: 2.8rem;
				line-height: 3.6rem;
				word-break: keep-all;
				word-wrap: break-word;
			}
		}
		&:hover {
			.brochure-card-hover {
				display: block;
				cursor: pointer;
			}
		}
	}
}

/* _AJOU Information */
.ajou-info {
	&-button {
		&--section {
			margin-top: 3rem;
			padding-right: 8rem;
			display: flex;
			flex-wrap: wrap;
			gap: 1.6rem;
			.middle--link {
				display: flex;
				align-items: center;
				justify-content: center;
				flex: 1;
				border: 0;
				border-radius: 1.2rem;
				overflow: hidden;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: cover;
				text-decoration: none;
				strong {
					display: block;
					width: 100%;
					color: $white;
					font-size: 3.2rem;
					line-height: 4.4rem;
					text-align: center;
					word-break: keep-all;
					word-wrap: break-word;
				}
				&.meta {
					background-image: url('../images/main/img-ajou-metacamp-bg.jpg');
				}
				&.solution {
					background-image: url('../images/main/img-solution-bg.jpg');
				}
			}
			.quick {
				&-menu {
					&--area {
						flex: 2;
						padding: 3rem 3.2rem 4.6rem;
						border-radius: 1.2rem;
						overflow: hidden;
						background-image: url('../images/main/img-quickmenu-bg.jpg');
						background-repeat: no-repeat;
						background-position: center center;
						background-size: cover;
						&.row {
							background-image: url('../images/main/img-quickmenu-rowtype-bg.jpg');
							padding: 3rem 3.2rem;
							.quick-menu--link {
								max-width: none;
								width: 100%;
								flex: none;
							}
						}
					}
					&-title {
						display: block;
						padding: 1.5rem 0 1.8rem;
						border-bottom: 1px solid rgba(255, 255, 255, 0.2);
						strong {
							color: $white;
							font-size: 3.2rem;
							font-weight: 600;
							line-height: 1.25;
						}
					}
					&-list {
						margin-top: 1.6rem;
					}
					&--link {
						display: inline-flex;
						flex: 33%;
						max-width: 33.3%;
						text-decoration: none;
						padding: 2rem 3.6rem 2rem 0;
						background: url('../images/common/icon-arrow-right-24-white.svg') no-repeat center right 1.2rem/2.4rem;
						span {
							color: $white;
							font-size: 2.4rem;
							line-height: 3.2rem;
							font-weight: 600;
						}
						&:hover {
							span {
								text-decoration: underline;
							}
						}
					}
				}
				&-link-list {
					margin-top: 1.6rem;
					display: flex;
					flex-wrap: wrap;
				}
			}
		}
		&--ver {
			flex: 1;
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			gap: 1.2rem;
			> a {
				flex: 1;
				border: 0;
				padding: 2.8rem 3.6rem;
				border-radius: 1.2rem;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: flex-start;
				gap: 3rem;
				text-decoration: none;
				strong {
					padding-right: 4.4rem;
					margin-top: auto;
					display: block;
					width: 100%;
					color: $white;
					font-size: 3.2rem;
					line-height: 4.4rem;
					text-align: left;
					word-break: keep-all;
					word-wrap: break-word;
				}
				&:hover {
					strong {
						background: url('../images/common/icon-arrowbar-right.svg') no-repeat bottom right/4.4rem;
					}
				}
			}
			.green--link {
				background-color: #31bc69;
			}
			.greenblue--link {
				background-color: #1db4cb;
			}
			.purple--link {
				background-color: $purple-100;
			}
		}
	}
}
/* subContents _0.enterInfo */
.sub {
	&-contents {
		flex: none;
		width: 100%;
	}
	&-content {
		position: relative;
		padding: 7.5rem 8rem 12rem 38.8rem;
		&-inner {
			&::after {
				content: '';
				display: block;
				width: 55rem;
				height: 21.7rem;
				position: absolute;
				right: 0;
				bottom: 0;
				background: $white url('../images/main/img-ajou-bg.png') no-repeat center center/ 100% 100%;
				z-index: -1;
			}
		}
		&.enterInfo {
			background: $white url('../images/main/img-flower-bg-2.png') no-repeat left 10.2rem bottom 20rem/8rem 8rem;
			&::before {
				content: '';
				display: block;
				width: 17.7rem;
				height: 16.6rem;
				position: absolute;
				left: -4.4rem;
				top: 0;
				background: $white url('../images/main/img-dot-bg.png') no-repeat center center/100% 100%;
			}
		}
		.content-title {
			> strong {
				display: inline-block;
				vertical-align: middle;
				color: $gray-800;
				font-size: 4rem;
				line-height: 4.8rem;
				font-weight: 700;
			}
			.swiper-wrapper {
				margin-top: 4rem;
			}
		}
	}
}
/* ajou youtube */
.ajou-youtube {
	&-swiper {
		.swiper-wrapper {
			margin-top: 2.8rem;
		}
		&-wrap {
			position: relative;
			&::before {
				content: '';
				display: block;
				width: 17rem;
				height: 17rem;
				position: absolute;
				top: 0;
				left: -14.4rem;
				background: url('../images/main/img-flower-bg.png') center center/100%;
			}
		}
		&-controller {
			position: absolute;
			right: 0;
			top: 1rem;
		}
		&-arrow {
			width: 4.8rem;
			height: 4.8rem;
			border: 1px solid $gray-300;
			border-radius: 100%;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			position: relative;
			cursor: pointer;
			&::before {
				content: '';
				display: block;
				width: 100%;
				height: 100%;
				background-repeat: no-repeat;
				background-size: 2.8rem 2.8rem;
				background-position: center center;
			}
			&[aria-disabled='true'] {
				cursor: not-allowed;
				opacity: 0.3;
			}
		}
		&-button {
			&-prev {
				&::before {
					background-image: url('../images/common/icon-arrow-left-24.svg');
				}
			}
			&-next {
				margin-left: 1.6rem;
				&::before {
					background-image: url('../images/common/icon-arrow-right-24.svg');
				}
			}
		}
	}
}
.youtube {
	&-card {
		display: block;
		max-width: 47.4rem;
		&--slide.swiper-slide {
			height: auto;
		}
		&--link {
			display: block;
			width: 100%;
			height: 100%;
			text-decoration: none;
		}
		&-img-wrapper {
			display: block;
			border-radius: 1.2rem;
			overflow: hidden;
			position: relative;
			> img {
				display: inline-block;
				width: 100%;
				height: 100%;
				vertical-align: top;
			}
			&::after {
				content: '';
				display: none;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 8.4rem;
				height: 8.4rem;
				align-items: center;
				justify-content: center;
				border-radius: 100%;
				box-shadow: 0 0.6rem 1.2rem 0 rgba(0, 0, 0, 0.251);
				background: $blue-100 url('../images/common/icon-play.svg') no-repeat center center/ 2.2rem 2.2rem;
			}
		}
		&-title {
			display: block;
			margin-top: 2.4rem;
			font-size: 2.4rem;
			font-weight: 600;
			text-decoration: none;
			word-break: keep-all;
			word-wrap: break-word;
		}
		&:hover {
			.youtube-card-img-wrapper::after {
				display: flex;
			}
		}
	}
}
