/* gaia */
.gaia-info {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;
	&-area {
		position: relative;
		display: flex;
		align-items: stretch;
		justify-content: space-between;
		gap: 11rem;
		max-height: 38.4rem;

		.image-wrapper {
			> img {
				display: block;
				width: auto;
				height: 100%;
				object-fit: cover;
				object-position: center;
			}
		}

		.content-title {
			> strong {
				display: inline-block;
				vertical-align: middle;
				color: $gray-800;
				font-size: 5rem;
				line-height: 5.8rem;
				font-weight: 700;
				&.blue-100 {
					color: $blue-100;
				}
			}
		}

		&::before {
			content: '';
			display: block;
			width: 17rem;
			height: 17rem;
			position: absolute;
			top: -1rem;
			left: -15rem;
			transform: translateY(-50%);
			background: url('../images/main/img-flower-bg.png') center center/100%;
		}
	}
	&--desc {
		p {
			margin-top: 2.4rem;
			color: $gray-700;
			font-size: 2.4rem;
			line-height: 3.2rem;
			font-weight: 400;
			& > strong {
				font-weight: 600;
			}
			& + p {
				margin-top: 3.6rem;
			}
		}
	}
	&-sns {
		margin-top: auto;
		white-space: nowrap;
		.sns--link {
			display: inline-block;
			padding: 2rem 2rem 2rem 5.2rem;
			text-decoration: none;
			background-repeat: no-repeat;
			background-position: center left 2rem;
			background-size: 2.4rem 2.4rem;
			border-radius: 1.2rem;
			&.sns--link {
				margin: 1rem 0.8rem 0 0;
			}
			span {
				color: $white;
				font-size: 2rem;
				line-height: 2.4rem;
			}
			&.facebook {
				background-color: $blue-100;
				background-image: url('../images/common/icon-facebook.svg');
			}
			&.instagram {
				background-color: $yellow-100;
				background-image: url('../images/common/icon-instagram.svg');
			}
			&.blog {
				background-color: #31bc69;
				background-image: url('../images/common/icon-blog.svg');
			}
		}
	}
}
/* // 5.  */

button {
	/* icon */
	&.btn-icon {
		position: relative;
		display: flex;
		align-items: center;
		&::before {
			content: '';
			display: block;
			background-color: inherit;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
		}
		span {
			display: inline-block;
			margin-left: 1rem;
		}
		&-only {
			span {
				display: none;
				margin: 0;
			}
		}
		&.notify--button {
			&::before {
				width: 3.1rem;
				height: 3.1rem;
				background-image: url('../images/common/icon-alarm.svg');
			}
		}
		&.menu--button {
			&::before {
				width: 1.4rem;
				height: 1.4rem;
				background-image: url('../images/common/icon-menu.svg');
			}
			&.close {
				&::before {
					background-image: url('../images/common/icon-menu-close.svg');
				}
			}
		}
		&.download--button {
			padding: 2.3rem;
			border-radius: 100%;
			overflow: hidden;
			margin-top: 50%;
			transform: translate(-50%, -50%);
			margin-left: 50%;
			&::before {
				width: 4rem;
				height: 4rem;
				background-image: url('../images/common/icon-download.svg');
			}
		}
	}
}
