	}
	&.chart {
		background-image: url('../images/common/icon-chart.svg');
	}
	&.fileDoc {
		background-image: url('../images/common/icon-fileDoc.svg');
	}
	&.book {
		background-image: url('../images/common/icon-book.svg');
	}
	&.docBag {
		background-image: url('../images/common/icon-docBag.svg');
	}
	&.doc {
		background-image: url('../images/common/icon-doc.svg');
	}
	&.building {
		background-image: url('../images/common/icon-building.svg');
	}
	&.close {
		width: 3.2rem;
		height: 3.2rem;
		background-image: url('../images/common/icon-close-32.svg');
