/* mo */
@media (max-width: 800px) {
	/* main */
	.main {
		/* main-body */
		&-body {
			min-width: auto;
			/* main > tabpage */
			.main-contents {
				flex: none;
				overflow: hidden;
				width: 100%;
				max-width: 100vw;
			}
		}
		/* left : main-tab-nav */
		&-tab {
			&--nav {
				display: none;
			}
		}
		/* right : main-contents */
		&-content {
			max-width: 100vw;
			padding: 8rem 0;
			/* main--tab contents */
			&--area {
				.content-title {
					padding: 0 2rem;
					> strong {
						font-size: 2.8rem;
						line-height: 3.6rem;
					}
					.viewAll--link {
						padding-right: 1.6rem;
						background-size: 1.6rem 1.6rem;
						> span {
							font-size: 1.6rem;
							line-height: 2rem;
						}
					}
				}
				& + .main-content--area {
					margin-top: 8rem;
				}
			}
		}
	}
	/* 0. ~ 4.  */
	/*   */
	.noti {
		/* swiper */
		&-swiper {
			&.swiper {
				margin-top: 2.4rem;
				padding-left: 2rem;
			}
			&-controller {
				margin-top: 1.6rem;
				max-width: 100%;
				align-items: center;
				justify-content: center;
				padding-right: 2rem;
				.swiper-scrollbar.noti-swiper-scrollbar {
					display: none;
				}
				.swiper-pagination-fraction {
					position: relative;
					top: 0;
					bottom: 0;
					font-size: 1.6rem;
					> span.swiper-pagination-current,
					& > span.swiper-pagination-total {
						font-size: 1.6rem;
					}
				}
			}
		}
		/* card */
		&-card {
			padding: 2.4rem;
			max-width: 30rem;
			height: 20rem;
			&-title {
				strong {
					font-size: 2rem;
					line-height: 2.4rem;
				}
			}
			&-info {
				flex: none;
				> span {
					font-size: 1.6rem;
					line-height: 2rem;
				}
			}
		}
	}

	/* 0._ */
	.brochure {
		&-swiper {
			margin-top: 2.4rem;
			padding: 0 2rem;
		}
		&-slide.swiper-slide {
			max-width: auto;
		}
		&-card {
			width: 26rem;
			height: 34rem;
			&-hover {
				display: block;
				position: relative;
				background-color: transparent;
				padding: 1.2rem 0 0 0;
				> strong {
					color: $gray-800;
					font-size: 2rem;
					line-height: 2.4rem;
					text-align: center;
				}
				button.btn-icon.download--button {
					display: none;
				}
			}
		}
	}

	/* _AJOU Information */
	.ajou-info {
		&-button {
			&--section {
				margin-top: 2.4rem;
				padding: 0 2rem;
				gap: 0.8rem;
				.middle--link {
					width: 100%;
					height: 15rem;
					flex: none;
					font-size: 2.4rem;
				}
				.quick {
					&-menu {
						&--area {
							flex: 1;
							padding: 2.4rem;
							&.row {
								padding: 3rem 3.2rem;
								.quick-menu--link {
									max-width: none;
									width: 100%;
									flex: none;
								}
							}
						}
						&-title {
							padding: 1.2rem 0;
							strong {
								font-size: 2.4rem;
								line-height: 3.2rem;
							}
						}
						&-list {
							margin-top: 2.4rem;
						}
						&--link {
							display: inline-flex;
							flex: 100%;
							max-width: 100%;
							text-decoration: none;
							padding: 1.6rem 2.4rem 1.6rem 0;
							span {
								font-size: 1.8rem;
								line-height: 1.8rem;
							}
						}
					}
				}
			}
			&--ver {
				width: 100%;
				flex: none;
				gap: 0.8rem;
				> a {
					flex-direction: row;
					align-items: center;
					padding: 2.8rem;
					strong {
						margin: 0;
						font-size: 2rem;
						line-height: 2.4rem;
						padding-right: 3rem;
						background: url(../images/common/icon-arrowbar-right.svg) no-repeat bottom right/2.4rem;
					}
					&:hover {
						strong {
							background: transparent;
						}
					}
				}
			}
		}
	}
	/* subContents _0.enterInfo */
	.sub {
		&-content {
			padding: 8rem 2rem 8rem;
			&-inner {
				&::after {
					width: 17.8rem;
					height: 7rem;
				}
			}
			&.enterInfo {
				background-position: left -4rem bottom 6rem;
				&::before {
					width: 6.3rem;
					height: 6rem;
					left: 0;
				}
			}
			.content-title {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				> strong {
					font-size: 2.8rem;
					line-height: 3.6rem;
					width: 100%;
					&:last-child {
						width: auto;
					}
				}
				.swiper-wrapper {
					margin-top: 2.4rem;
				}
			}
		}
	}
	/* ajou youtube */
	.ajou-youtube {
		&-swiper {
			.swiper-wrapper {
				margin-top: 2.4rem;
			}
			&-wrap {
				&::before {
					width: 8rem;
					height: 8rem;
					left: 50%;
					transform: translateX(-50%);
				}
			}
			&-controller {
				top: 3.6rem;
			}
			&-arrow {
				width: 3.6rem;
				height: 3.6rem;
			}
		}
	}
	.youtube {
		&-card {
			&-title {
				margin-top: 1.2rem;
				font-size: 2rem;
				font-weight: 600;
				text-align: center;
			}
		}
	}
	/* // 0. */

	/* subContents 5. _ */
	.program-apply {
		&-area {
			height: auto;
			flex-direction: column;
			width: calc(100% - 4rem);
			margin: 0 auto;
		}
		&--link {
			flex-direction: row;
			gap: 0.8rem;
			strong {
				font-size: 2rem;
				line-height: 2.4rem;
			}
		}
	}
	/* gaia */
	.gaia-info {
		flex: none;
		width: 100%;
		&-area {
			flex-direction: column;
			gap: 0;
			max-height: none;
			.image-wrapper {
				position: absolute;
				top: 7rem;
				max-width: 40rem;
				> img {
					display: inline-block;
					width: 100%;
					height: 100%;
				}
			}

			.content-title {
				> strong {
					font-size: 2.8rem;
					line-height: 3.6rem;
					width: auto;
					flex: none;
					gap: 0.4rem;
				}
			}

			&::before {
				width: 8rem;
				height: 8rem;
				top: 0;
				left: 20rem;
			}
		}
		&--desc {
			margin-top: 24.8rem;
			p {
				margin-top: 2.4rem;
				font-size: 1.8rem;
				line-height: 2.4rem;
				& > strong {
					font-weight: 600;
				}
				& + p {
					margin-top: 1.2rem;
				}
			}
		}
		&-sns {
			margin-top: 4rem;
			display: flex;
			align-items: center;
			gap: 0.4rem;
			.sns--link {
				padding: 1.4rem 0;
				flex: 1;
				&.sns--link {
					margin: 0;
					background-image: none;
				}
				span {
					text-align: center;
					font-size: 1.6rem;
					line-height: 2rem;
					width: 100%;
					display: inline-block;
				}
			}
		}
	}
	/* // 5.  */
	.ico {
		width: 3.2rem;
		height: 3.2rem;
		&.youtube-logo {
			width: 3.6rem;
			height: 3.6rem;
			margin-left: 0;
			background-size: 100% 2.4rem;
		}
	}
	/* bottom sheet */
	.mo-tab {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 1;
		&.fixed {
			position: fixed;
			top: 6.4rem;
			bottom: auto;
			> button {
				background-color: $primary-500;
			}
		}

		&-nav--button {
			width: 100%;
			border: 0;
			border-radius: 0;
			padding: 2rem 1.6rem 2rem 1.6rem;
			justify-content: flex-start;
			background: rgba(0, 0, 0, 0.24) url('../images/common/icon-arrow-down-20-white.svg') no-repeat center right
				1.6rem /2rem;

			> span {
				color: $white;
				font-size: 1.6rem;
				line-height: 2rem;
				font-weight: 500;
			}
		}
	}
	.tab-nav--bottomsheet {
		display: none;
		z-index: 2;
		&.active {
			display: block;
			width: 100%;
			height: 100%;
			.bottomsheet {
				z-index: 5;
				position: fixed;
				bottom: 0;
				left: 0;
				right: 0;
				background-color: $white;
				border-top: 1px solid $gray-100;
				border-radius: 1.2rem 1.2rem 0 0;
				&-header {
					padding: 2rem 1.6rem;
					display: flex;
					align-items: center;
					justify-content: space-between;
					strong {
						flex: none;
						font-size: 1.8rem;
						line-height: 2.4rem;
						color: $gray-900;
					}
					.close--button {
						border: none;
						padding: 0;
						width: 3.2rem;
						height: 3.2rem;
					}
				}
				&-menu {
					&--link {
						display: inline-block;
						width: 100%;
						height: 100%;
						padding: 2rem 1.2rem;
						text-decoration: none;
						border-top: 1px solid $gray-100;
						> strong {
							font-size: 1/6rem;
							line-height: 2rem;
						}
					}
				}
			}
			& > .dim {
				position: fixed;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background-color: rgba(0, 0, 0, 0.6);
				z-index: 3;
			}
		}
	}
}
